import './theme/index.scss';

import { jst_row as row, jst_col as col } from './row';
import {jst_button as button, jst_button_group as button_group} from './button';
import { jst_cell as cell, jst_cell_group as cell_group } from "./cell";
import field from './field';
import overlay from './overlay';
import popup from './popup';
import toast from './toast';
import icon from './icon';
import { jst_picker as picker, jst_picker_column as pickerColumn } from "./picker";
import loading from './loading';
import {jst_checkbox as checkbox, jst_checkbox_group as checkbox_group} from './checkbox';
import datetimePicker from './datetime-picker';
import { jst_radio as radio, jst_radio_group as radio_group } from "./radio";
import switchs from './switchs';
import switch_cell from './switchcell';
import slide from './slider';
import { jst_collapse as collapse, jst_collapseItem as collapseItem } from "./collapse";
import { jst_tab as tab, jst_tabs as tabs } from './tab';
import rate from './rate';
import search from './search';
import stepper from './stepper';
import progress from './progress';

import { jst_step as step, jst_steps as steps } from "./step";
import panel from './panel';
import list from './list';
import { jst_swiper as swiper, jst_swiperItem as swiperItem } from "./swipe";
import Lazyload from './lazyload';
import info from './info';
import { jst_badge as badge, jst_badge_group as badgeGroup } from "./badge";
import { jst_nav_bar as navBar } from "./nav-bar";
import { jst_tabbar as tabbar, jst_tabbarItem as tabbarItem} from "./tabbar";
import { jst_pullRefresh as pullRefresh } from "./pull-refresh";
import { jst_tag as tag} from "./tag";
import { jst_pagination as pagination } from "./pagination";
import circle from './circle';
import actionsheet from './actionsheet';
import noticebar from './notice-bar';
import dialog from './dialog';
import notify from './notify';
import swipecell from './swipe-cell';
import uploader from './uploader';
import { jst_numberkeyboard as numberkeyboard, jst_keys as keys } from "./number-keyboard";
import passwordinput from './password-input';
import treeSelect from './tree-select';
import imagePreview from './image-preview';





const components = [
    row,
    col,
    button,
    button_group,
    cell,
    cell_group,
    field,
    overlay,
    popup,
    radio,
    radio_group,
    switchs,
    switch_cell,
    loading,
    //checkbox,
    checkbox,
    checkbox_group,
    toast,
    icon,
    picker,
    pickerColumn,
    datetimePicker,
    slide,
    collapse,
    collapseItem,
    tab,
    tabs,
    rate,
    progress,
    step,
    steps,
    panel,
    list,
    swiper,
    swiperItem,
    // Lazyload, 不自动进行安装 可能需要传入参数
    search,
    stepper,
    info,
    badge,
    badgeGroup,
    navBar,
    tabbar,
    tabbarItem,
    pullRefresh,
    tag,
    pagination,
    circle,
    actionsheet,
    noticebar,
    dialog,
    notify,
    swipecell,
    uploader,
    numberkeyboard,
    keys,
    passwordinput,
    treeSelect,
    imagePreview,
];
const install = function(Vue) {
    if (install.installed) return;
    components.map(component => {
        Vue.use(component);
    });
};

//  全局引用可自动安装
if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}

const version = '1.0.0';


export {
    install,
    version,
    row,
    col,
    button,
    button_group,
    cell,
    cell_group,
    field,
    overlay,
    popup,
    toast,
    icon,
    picker,
    pickerColumn,
    datetimePicker,
    loading,
    checkbox,
    checkbox_group,
    radio,
    radio_group,
    switchs,
    switch_cell,
    slide,
    collapse,
    collapseItem,
    tab,
    tabs,
    rate,
    progress,
    step,
    steps,
    panel,
    list,
    swiper,
    swiperItem,
    Lazyload,
    search,
    stepper,
    info,
    badge,
    badgeGroup,
    navBar,
    tabbar,
    tabbarItem,
    pullRefresh,
    tag,
    pagination,
    circle,
    actionsheet,
    noticebar,
    dialog,
    notify,
    swipecell,
    uploader,
    numberkeyboard,
    keys,
    passwordinput,
    treeSelect,
    imagePreview,
}

export default {
    install,
    version
};
