<template>
    <div :class="bem('search', { 'show-action': showAction || useActionSlot })" :style="'background:' + background">
        <div :class="bem('search__content', [shape])">
            <div class="jst-search__label" v-if="label">{{ label }}</div>
            <slot v-else name="label" />

            <jst-field
                    clearable
                    v-model="inputValue"
                    :left-icon="leftIcon"
                    :focus=" focus "
                    :error=" error "
                    :border="border "
                    class="jst-search__field"
                    :disabled=" disabled "
                    :readonly=" readonly "
                    :maxlength=" maxlength "
                    :input-align="inputAlign "
                    :placeholder=" placeholder "
                    style="padding: 5px 10px 5px 0; background-color: transparent;"
                    @blur="onBlur"
                    @focus="onFocus"
                    @change="onChange"
                    @search="onSearch"
                    @clear="onClear"
            />
        </div>

        <div v-if="showAction || useActionSlot" class="jst-search__action">
            <slot v-if="useActionSlot" name="action" />
            <div v-else @click="onClear">取消</div>
        </div>
    </div>
</template>

<script>
    import utils from "./../tool/utils";

    export default {
        name: "jst-search",
        data(){
            return{
                showClear: false,
                inputValue: ''
            }
        },

        props: {
            leftIcon:{
                type:String,
                default:"iconfont icon-chaxun"
            },
            value: {
                type: String,
                default: ''
            },
            focus: Boolean,
            error: Boolean,
            disabled: Boolean,
            readonly: Boolean,
            inputAlign: String,
            showAction: Boolean,
            useActionSlot: Boolean,
            placeholder: String,
            background: {
                type: String,
                default: '#ffffff'
            },
            maxlength: {
                type: Number,
                default: -1
            },
            shape: {
                type: String,
                default: 'square'
            },
            label: String,
            border:Boolean
        },

        watch: {
          value(){
              this.inputValue = this.value;
              //console.log(this.inputValue, "watch");
          }
        },

        created(){
          this.$nextTick(()=>{
              this.inputValue = this.value;
          })
        },

        methods: {
            onChange(value) {
                this.inputValue = value;
                this.$emit('change', value);
                this.$emit('input', value);
                //console.log(this.inputValue, "change");
            },

            onCancel() {
                this.inputValue = '';
                this.$emit('input', '');
                this.$emit('cancel',);
                this.$emit('change', '');
            },

            onSearch() {
                this.$emit('search', this.inputValue);
            },

            onFocus() {
                this.$emit('focus');
            },
            onBlur() {
                this.$emit('blur');
            },
            onClear() {
                this.inputValue = '';
                this.$emit('input', '');
                this.$emit('clear');
            },

            bem: utils.bem
        }
    }
</script>

<style scoped>

</style>

