<template>
    <div class="jst-tree-select" :style="{ height: `${height}px` }" >
        <div class="jst-tree-select__nav">
            <div
                    v-for="(item, index) in items"
                    :key="index"
                    :class="[
                        'jst-ellipsis',
                        bem('tree-select__nitem', {
                            active: mainActiveIndex === index,
                            disabled: item.disabled
                        })
                    ]"
                    @click="onNavClick(item, index)"
            >
                {{ item.text }}
            </div>
        </div>
        <div class="jst-tree-select__content">
            <div
                    v-for="(item) in subItems"
                    :key="item.id"
                    :class="[
                        'jst-ellipsis',
                        bem('tree-select__item', {
                            active: activeId === item.id,
                            disabled: item.disabled
                        })
                    ]"
                    @click="onItemClick(item)"
                    >
                {{ item.text }}
                <jst-icon v-if="activeId === item.id" name="checkeds" size="16px" class="jst-tree-select__selected" ></jst-icon>
            </div>
        </div>
    </div>
</template>

<script>
    import utils from "./../tool/utils";

    export default {
        name: "jst-tree-select",

        props: {
            items: Array,
            mainActiveIndex: Number,
            activeId: {
                type: [Number, String],
                default: 0
            },
            height: {
                type: Number,
                default: 300
            }
        },

        computed: {
            subItems(){
                const temp_selectedItem = this.items[this.mainActiveIndex] || {};
                const temp_subItems = temp_selectedItem.children || [];
                return temp_subItems;
            }
        },

        methods: {
            bem: utils.bem,

            onItemClick(item){
                if (!item.disabled) {
                    this.$emit('itemclick', item);
                }
            },

            onNavClick(item, index){
                if (!item.disabled) {
                    this.$emit('navclick', index);
                }
            }
        }

    }
</script>

