<template>
    <div
            class="jst-checkbox"
            @click="checkboxClick($event)">
        <div
                :class="bem('checkbox__icon', [shape, { disabled: isDisabled, checked: checked }])"
                @click="onClickIcon">
                <slot name="icon">
                    <jst-icon class="jst-icon" name="success" :style="iconStyle" />
                </slot>
        </div>
        <span
                :class="bem('checkbox__label', [labelPosition, { disabled: isDisabled }])"
                @click="onClickLabel"
        >
            <slot></slot>
        </span>
    </div>
</template>

<script>
    import { CheckboxMixin } from '../mixins/checkbox';
    import utils from './../tool/utils';


    export default {
        name: "jst-checkbox",

        mixins: [CheckboxMixin('jst-checkbox-group', utils.bem)],


        computed: {
            checked: {
                get() {
                    return this.parent ? this.parent.value.indexOf(this.name) !== -1 : this.value;
                },

                set(val) {
                    if (this.parent) {
                        this.setParentValue(val);
                    } else {
                        this.$emit('input', val);
                    }
                }
            }
        },

        watch: {
            value(val) {
                this.$emit('change', val);
            }
        },

        methods: {
            bem: utils.bem,
            suffixPx: utils.suffixPx,
            toggle() {
                const checked = !this.checked;

                // When toggle method is called multiple times at the same time,
                // only the last call is valid.
                // This is a hack for usage inside Cell.
                clearTimeout(this.toggleTask);
                this.toggleTask = setTimeout(() => {
                    this.checked = checked;
                });
            },

            onClickIcon() {
                if (!this.isDisabled) {
                    this.toggle();
                }
            },

            onClickLabel() {
                if (!this.isDisabled && !this.labelDisabled) {
                    this.toggle();
                }
            },

            setParentValue(val) {
                const { parent } = this;
                const value = parent.value.slice();

                if (val) {
                    if (parent.max && value.length >= parent.max) {
                        return;
                    }

                    /* istanbul ignore else */
                    if (value.indexOf(this.name) === -1) {
                        value.push(this.name);
                        parent.$emit('input', value);
                    }
                } else {
                    const index = value.indexOf(this.name);

                    /* istanbul ignore else */
                    if (index !== -1) {
                        value.splice(index, 1);
                        parent.$emit('input', value);
                    }
                }
            },

            checkboxClick(event){
                event.stopPropagation();
                this.$emit('click', event);
            },
        }
    }
</script>

