import Vue from 'vue';
import JstNotify from './notify';
import { RED, WHITE } from './../tool/color';
import { isObj, isServer } from './../tool/tools';
import { mount } from './../tool/functional';


let timer;
let instance;

const parseOptions = message => (isObj(message) ? message : {message});

function Notify(options = {}) {
    /* istanbul ignore if */
    if (isServer) {
        return;
    }

    if (!instance) {
        instance = mount(JstNotify, {
            on: {
                click(event) {
                    if (instance.onClick) {
                        instance.onClick(event);
                    }
                }
            }
        });
    }

    options = {
        ...Notify.currentOptions,
        ...parseOptions(options)
    };

    Object.assign(instance, options);
    clearTimeout(timer);

    if (options.duration && options.duration > 0) {
        timer = setTimeout(Notify.clear, options.duration);
    }

    return instance;
}

function defaultOptions(){
    return {
        value: true,
        message: 'content',
        color: WHITE,
        background: RED,
        duration: 3000,
        zIndex:110,
        className: '',
        onClick: null
    };
}

Notify.clear = () => {
    if (instance) {
        instance.value = false;
    }
};

Notify.currentOptions = defaultOptions();

Notify.setDefaultOptions = (options) => {
    Object.assign(Notify.currentOptions, options);
};

Notify.resetDefaultOptions = () => {
    Notify.currentOptions = defaultOptions();
};

export default function jst_toast (Vue) {
    Vue.component(JstNotify.name, JstNotify);
    Vue.prototype.$notify = Notify;
};
