<template>
    <div>
        <div
                class="jst-swipe-cell"
                @click="onClick('cell')"
                @touchstart="startDrag"
                @touchmove="onDrag"
                @touchend="endDrag"
                @touchcancel="endDrag"
        >
            <div
                    :class="bem('swipe-cell__wrapper')"
                    :style="wrapperStyle"
                    @transitionend="swiping = false"
            >
                <div v-if="leftWidth" :class="bem('swipe-cell__left')" @click="onClick('left', true)">
                    <slot name="left"></slot>
                </div>
                <slot></slot>
                <div v-if="rightWidth" :class="bem('swipe-cell__right')" @click="onClick('right', true)">
                    <slot name="right"></slot>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { TouchMixin } from '../mixins/touch';
    import { ClickOutsideMixin } from '../mixins/click-outside';
    import utils from './../tool/utils';
    import { range } from './../tool/tools';
    export default {
        mixins: [TouchMixin, ClickOutsideMixin({
            event: 'touchstart',
            method: 'onClick'
        })],
        name: "jst-swipe-cell",
        data(){
            return{
                offset: 0,
                dragging: false,
                THRESHOLD: 0.15,
            }
        },
        props: {
            onClose: Function,
            disabled: Boolean,
            leftWidth: Number,
            rightWidth: Number
        },
        computed: {
            wrapperStyle: function(){
                return {
                    'transform': `translate3d(${this.offset}px, 0, 0)`,
                    'transition': this.dragging ? 'none' : '.6s cubic-bezier(0.18, 0.89, 0.32, 1)'
                };
            },

        },
        methods: {
            open(position) {
                const offset = position === 'left' ? this.leftWidth : -this.rightWidth;
                this.swipeMove(offset);
                this.resetSwipeStatus();
            },

            close() {
                this.offset = 0;
            },

            resetSwipeStatus() {
                this.swiping = false;
                this.opened = true;
            },

            swipeMove(offset = 0) {
                this.offset = range(offset, -this.rightWidth, this.leftWidth);
                if (this.offset) {
                    this.swiping = true;
                } else {
                    this.opened = false;
                }
            },

            swipeLeaveTransition(direction) {
                const { offset, leftWidth, rightWidth } = this;
                const threshold = this.opened ? 1 - this.THRESHOLD : this.THRESHOLD;

                // right
                if (direction === 'right' && -offset > rightWidth * threshold && rightWidth > 0) {
                    this.open('right');
                    // left
                } else if (direction === 'left' && offset > leftWidth * threshold && leftWidth > 0) {
                    this.open('left');
                    // reset
                } else {
                    this.swipeMove(0);
                }
            },

            startDrag(event) {
                if (this.disabled) {
                    return;
                }
                this.dragging = true;
                this.startOffset = this.offset;
                this.touchStart(event);
            },

            onDrag(event) {
                if (this.disabled) {
                    return;
                }

                this.touchMove(event);

                if (this.direction === 'horizontal') {
                    event.preventDefault();
                    this.swipeMove(this.deltaX + this.startOffset);
                }
            },

            endDrag() {
                if (this.disabled) {
                    return;
                }

                this.dragging = false;
                if (this.swiping) {
                    this.swipeLeaveTransition(this.offset > 0 ? 'left' : 'right');
                }
            },

            onClick(position = 'outside') {
                this.$emit('click', position);

                if (!this.offset) {
                    return;
                }

                if (this.onClose) {
                    this.onClose(position, this);
                } else {
                    this.swipeMove(0);
                }
            },
            bem: utils.bem
        },
    }
</script>

<style scoped>
    .jst-swipe-cell {
        overflow: hidden;
        position: relative;
    }
    .jst-swipe-cell__left,
    .jst-swipe-cell__right {
        top: 0;
        height: 100%;
        position: absolute;
    }
    .jst-swipe-cell__left {
        left: 0;
        transform: translate3d(-100%, 0, 0);
    }
    .jst-swipe-cell__right {
        right: 0;
        transform: translate3d(100%, 0, 0);
    }

</style>