<template>
    <div
            :style="{ background: inactiveColor }"
            :class="sliderClass"
            @click="onClick($event)"
    >
        <div class="jst-slider__bar" :style="barStyle">
            <div
                    class="jst-slider__button-wrapper"
                    @touchstart="onTouchStart($event)"
                    @touchmove="onTouchMove($event)"
                    @touchend="onTouchEnd($event)"
                    @touchcancel="onTouchEnd($event)"
            >
                <slot name="button">
                    <div class="jst-slider__button" ></div>
                </slot>
            </div>
        </div>
    </div>
</template>

<script>
    import { TouchMixin } from '../mixins/touch';
    import utils from './../tool/utils';

    export default {
        name: "jst-slider",

        mixins: [TouchMixin],

        props: {
            min:{
                type: Number,
                default: 0
            },
            value: {
                type: Number,
                default: 0
            },
            disabled: Boolean,
            vertical: Boolean,
            activeColor: String,
            inactiveColor: String,
            max: {
                type: Number,
                default: 100
            },
            step: {
                type: Number,
                default: 1
            },
            barHeight: {
                type: String,
                default: '2px'
            }
        },

        computed: {
            sliderClass(){
                return utils.bem('slider', [{ disabled: this.disabled, vertical: this.vertical }]);
            },

            barStyle(){
                console.log(this.value)
                const mainAxis = this.vertical ? 'height' : 'width';
                const crossAxis = this.vertical ? 'width' : 'height';

                const barStyle = {
                    [mainAxis]: `${this.format(this.value)}%`,
                    [crossAxis]: this.barHeight,
                    background: this.activeColor
                };

                console.log(barStyle)
                return barStyle;
            }
        },

        created(){
            console.log(this.value)
        },

        methods: {
            onTouchStart(event) {
                if (this.disabled) return;
                console.log(event);
                this.touchStart(event);
                this.startValue = this.format(this.value);
            },

            onTouchMove(event) {
                event.preventDefault();
                event.stopPropagation();

                if (this.disabled) return;

                this.touchMove(event);

                const rect = this.$el.getBoundingClientRect();
                const delta = this.vertical ? this.deltaY : this.deltaX;
                const total = this.vertical ? rect.height : rect.width;
                const diff = (delta / total) * 100;

                this.newValue = this.startValue + diff;
                this.updateValue(this.newValue);
            },

            onTouchEnd() {
                if (this.disabled) return;
                this.updateValue(this.newValue, true);
            },

            onClick(event) {
                event.stopPropagation();

                if (this.disabled) return;

                const rect = this.$el.getBoundingClientRect();
                const delta = this.vertical ? event.clientY - rect.top : event.clientX - rect.left;
                const total = this.vertical ? rect.height : rect.width;
                const value = (delta / total) * 100;

                this.updateValue(value, true);
            },

            updateValue(value, end) {
                value = this.format(value);
                this.$emit('input', value);

                if (end) {
                    this.$emit('change', value);
                }
            },

            format(value) {
                return (
                    Math.round(Math.max(this.min, Math.min(value, this.max)) / this.step) * this.step
                );
            }
        }

    }
</script>

