<template>
    <div class=jst-password-input>
        <ul
                :class="bem('password-input__security ') + 'jst-hairline--surround'"
                @touchstart="TouchEvent"
        >
            <li class="jst-hairline" v-for="index in length">
                <i v-if="mask===true" :style="{'visibility' : value[index-1] ? 'visible' : 'hidden'}"></i>
                <span v-else>{{value[index-1]}}</span>
            </li>
        </ul>
        <div v-if="info" :class="bem(errorInfo ? 'error-info' : 'password-input__info')">{{info}}</div>
    </div>
</template>

<script>
    import utils from './../tool/utils'
    export default {
        name: "jst-password-input",
        data(){
            return{

            }
        },
        props: {
            info: String,
            errorInfo: String,
            mask: {
                type: Boolean,
                default: true
            },
            value: {
                type: String,
                default: ''
            },
            length: {
                type: Number,
                default: 6
            }
        },
        methods: {
            TouchEvent (event){
                event.stopPropagation();
                this.$emit('focus', event);
            },
            bem: utils.bem,
        }
    }
</script>

