<template>
    <div>
        <jst-overlay
                v-if="mask || forbidClick"
                v-show="value"
                :mask="mask"
                :z-index="zIndex"
        ></jst-overlay>
        <transition name="jst-fade">
            <div
                    v-show="value"
                    :style="'z-index:' + zIndex"
                    :class="'jst-toast jst-toast__container jst-toast--' + (type === 'text' ? 'text' : 'icon') + ' jst-toast--' + position"
                    @touchmove="noop"
            >
                <!-- text only -->
                <span v-if="type === 'text'">{{ message }}</span>

                <!-- with icon -->
                <template v-else>
                    <jst-loading
                            v-if="type === 'loading'"
                            color="white"
                            :type="loadingType"
                            custom-class="jst-toast__loading"
                    ></jst-loading>
                    <!--icon span -->
                    <div v-else>
                        <jst-icon  class="jst-toast__icon" :name="type" ></jst-icon>
                    </div>

                    <span v-if="message" class="jst-toast__text">{{ message }}</span>
                </template>
                <slot />
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "jst-toast",

        props: {
            value: Boolean,
            mask: Boolean,
            message: String,
            forbidClick: Boolean,
            zIndex: {
                type: Number,
                default: 1000
            },
            type: {
                type: String,
                default: 'text'
            },
            loadingType: {
                type: String,
                default: 'circular'
            },
            position: {
                type: String,
                default: 'middle'
            },
            icon: {
                type: String,
                default: ''
            }
        },

        computed: {
          iconClass(){
              let icon_class = '';
              if(this.type=="success"){
                  icon_class = "iconfont "
              }
              return icon_class
          }
        },

        methods: {
            // for prevent touchmove
            noop() {},

            updateZIndex() {
                this.$nextTick(() => {
                    this.$el.style.zIndex = this.zIndex + 1;
                });
            }
        },
    }
</script>

