import Vue from 'vue';
import VueImagePreview from './image-preview';
import { isServer } from './../tool/tools';

let instance;

const defaultConfig = {
    images: {
        type: Array,
        default: ()=>[]
    },
    loop: true,
    value: true,
    minZoom: 1 / 3,
    maxZoom: 3,
    className: '',
    lazyLoad: false,
    showIndex: true,
    asyncClose: false,
    startPosition: 0,
    showIndicators: false
};

const initInstance = () => {
    instance = new (Vue.extend(VueImagePreview))({
        el: document.createElement('div')
    });
    document.body.appendChild(instance.$el);
};

const ImagePreview = (images, startPosition = 0) => {
    /* istanbul ignore if */
    if (isServer) {
        return;
    }

    if (!instance) {
        initInstance();
    }

    const options = Array.isArray(images) ? { images, startPosition } : images;

    Object.assign(instance, defaultConfig, options);

    instance.$once('input', show => {
        instance.value = show;
    });

    if (options.onClose) {
        instance.$once('close', options.onClose);
    }

    return instance;
};

export default function jst_imagePreview(Vue) {
    Vue.component(VueImagePreview.name, VueImagePreview);
    Vue.prototype.$imagePreview = ImagePreview;
}
