<template>
    <div
            :class="classes"
            :style="customStyle"
            @click="onClick"
    >
        <span v-if="icon" :name="icon" :class="'jst-cell__left-icon-wrap ' + icon" ></span>
        <slot v-else name="icon" ></slot>

        <div :style="titleWidth ? ('max-width:' + titleWidth + ';min-width:' + titleWidth) : ''" :class="'jst-cell__title ' + titleClass" >
            <template v-if="title">{{ title }}</template>
            <slot v-else name="title" ></slot>

            <div v-if="label || useLabelSlot" :class="'jst-cell__label ' + labelClass">
                <slot v-if="useLabelSlot" name="label" />
                <template v-else-if="label">{{ label }}</template>
            </div>
        </div>

        <div :class="'jst-cell__value '  + valueClass">
            <template v-if="value || value === 0">{{ value }}</template>
            <slot v-else ></slot>
        </div>

        <!--custom-class="jst-cell__right-icon"-->
        <!--<span-->
                <!--v-if="isLink"-->
                <!--:name="(arrowDirection ? 'arrow' + '-' + arrowDirection : 'arrow')"-->
                <!--:class="'jst-cell__right-icon-wrap ' + (arrowDirection ? 'iconfont icon-you' : 'iconfont icon-xiangxia1')"-->
        <!--&gt;</span>-->
        <span
                v-if="isLink"
                :name="(arrowDirection ? 'arrow' + '-' + arrowDirection : 'arrow')"
                :class="'jst-cell__right-icon-wrap ' + (arrowDirection ? 'iconfont icon-xiangxia1' :  'iconfont icon-you')"
        ></span>
        <slot v-else name="right-icon" />

        <slot name="extra" ></slot>
    </div>
</template>

<script>
    import link from './../mixins/link';
    import utils from './../tool/utils'
    import { cellProps } from './shared'

    export default {
        name: "jst-cell",
        mixins: [ link ],
        data(){
          return {
              // a: '张三'
          }
        },
        props: {
            ...cellProps,

            clickable: Boolean,
            arrowDirection: String,
            valueClass: {
                type: String,
                default: '',
            },
            titleClass: {
                type: String,
                default: '',
            },
            labelClass: {
                type: String,
                default: '',
            }
        },
        computed: {
          classes: function(){
              return utils.bem('cell', [this.size, { center: this.center, required: this.required, borderless: !this.border, clickable: this.isLink || this.clickable }]);
            }
        },

        created(){
            //console.log(this.titleWidth);
        },

        methods: {
            onClick: function (e) {
                this.$emit('click', e);
                this.jumpLink();
            }
        }
    }
</script>

<style scoped>
    .jst-panel__header-value {
        color: #ed5565;
    }
</style>
