<template>
    <div>
        <jst-popup
                :show="value"
                :z-index="zIndex"
                :overlay="overlay"
                custom-class="center"
                :transition="transition"
                :close-on-click-overlay="closeOnClickOverlay"
                @close="onClickOverlay"
                class="jst-dialog-popup"
        >
            <div v-show="value" :class="['jst-dialog', className]">
                <div :class="bem('dialog__header', { isolated: !message && !slots() })">{{title}}</div>
                <div :class="bem('dialog__content')">
                    <slot></slot>
                    <div :class="classes">
                        {{message}}
                    </div>
                    <!--<div :domPropsInnerHTML=""-->
                         <!--:class="classes"-->
                    <!--/>-->
                </div>
                <div :class="fotclass">
                    <jst-button
                            v-if="showCancelButton"
                            size="large"
                            :class="bem('dialog__cancel')"
                            :loading="loading.cancel"
                            :style="cancelStyle"
                            @click="handleAction('cancel')"
                    >
                        {{cancelButtonText || '取消'}}
                    </jst-button>
                    <jst-button
                            v-if="showConfirmButton"
                            size="large"
                            :class="leftclass"
                            :loading="loading.confirm"
                            :style="confirmStyle"
                            @click="handleAction('confirm')"
                    >
                        {{confirmButtonText || '确认'}}
                    </jst-button>
                </div>
            </div>
        </jst-popup>

    </div>
</template>

<script>
    import utils from './../tool/utils'
    import { SlotsMixin } from '../mixins/slots';
    export default {
        name: "jst-dialog",
        mixins: [ SlotsMixin ],
        data(){
            return{
                loading: {
                    confirm: false,
                    cancel: false
                }
            }
        },
        props: {
            title: String,
            message: String,
            value: Boolean,
            className: null,
            callback: Function,
            beforeClose: Function,
            messageAlign: String,
            cancelButtonText: String,
            cancelButtonColor: String,
            confirmButtonText: String,
            confirmButtonColor: String,
            show: {
                type: Boolean,
                default: true
            },
            zIndex: {
                type: Number,
                value: 2000
            },
            showCancelButton: {
                type: Boolean,
                default: true
            },
            showConfirmButton: {
                type: Boolean,
                default: true
            },
            overlay: {
                type: Boolean,
                default: true
            },
            transition: {
                type: String,
                value: 'scale'
            },
            closeOnClickOverlay: {
                type: Boolean,
                default: false
            }
        },
        computed: {
            cancelStyle: function(){
                return {
                    'color' : this.cancelButtonColor,
                };
            },
            confirmStyle: function(){
                return {
                    'color' : this.confirmButtonColor,
                };
            },
            classes: function(){
                return utils.bem('dialog__message', { 'has-title': this.title, messageAlign: this.messageAlign });
            },
            fotclass: function () {
                const hasButtons = this.showCancelButton && this.showConfirmButton;
                return utils.bem('dialog__footer', { buttons: hasButtons }) + ' jst-hairline--top';
            },
            leftclass: function () {
                const hasButtons = this.showCancelButton && this.showConfirmButton;
                // return utils.bem('dialog__confirm', { 'jst-hairline--left': hasButtons });
                return utils.bem('dialog__confirm', { buttons: hasButtons }) + ' jst-hairline--left';
            },
        },

        created(){
          this.$nextTick(()=>{
              // console.log(this.title);
          })
        },

        methods: {
            onClickOverlay() {
                this.handleAction('overlay');
            },
            handleAction(action) {
                console.log(action);
                this.$emit(action);
                if (this.beforeClose) {
                    this.loading[action] = true;
                    this.beforeClose(action, state => {
                        if (state !== false) {
                            this.onClose(action);
                        }
                        this.loading[action] = false;
                    });
                } else {
                    this.onClose(action);
                }
            },
            onClose(action) {
                this.close();
                if (this.callback) {
                    this.callback(action);
                }
            },
            close(){
                // this.value = false;
                // this.$emit("update:value", false);
                this.$emit('input', false);
            },
            bem: utils.bem
        },
    }
</script>

