<template>
    <jst-cell
            :icon="leftIcon"
            :title="label"
            :center="center"
            :border="border"
            :is-link="isLink"
            :required="required"
            :style="customStyle"
            :title-width="titleWidth"
            class="jst-field"
            :size="size"
    >
        <slot name="left-icon" slot="icon" />
        <slot name="label" slot="title" />
        <div :class="'jst-field__body ' + (type === 'textarea' ? 'jst-field__body--textarea' : '') ">
            <!--@input="onInput" :auto-height="autosize"-->
            <textarea
                    v-if="type === 'textarea'"
                    :class="inputClass"
                    :fixed="fixed"
                    :focus="focus"
                    v-model="fieldValue"
                    :disabled="disabled || readonly"
                    :maxlength="maxlength"

                    :cursor-spacing="cursorSpacing"
                    :adjust-position="adjustPosition"
                    :show-confirm-bar="showConfirmBar"
                    :placeholder="placeholder"

                    @blur="onBlur"
                    @focus="onFocus"
                    @confirm="onConfirm"
            >
              <div v-if="fieldValue == null || fieldValue.length === 0" :style="placeholderStyle" :class="placeholderClass">
                {{ placeholder }}
              </div>
            </textarea>
            <!--@input="onInput"-->
            <input
                    v-else
                    :class="'input-class ' + inputClass"
                    :type="type"
                    :focus="focus"
                    v-model="fieldValue"
                    :disabled="disabled || readonly"
                    :maxlength="maxlength"
                    :placeholder="placeholder"
                    :placeholder-style="placeholderStyle"
                    :placeholder-class="error ? 'jst-field__placeholder--error' : 'jst-field__placeholder'"
                    :confirm-type="confirmType"
                    :confirm-hold="confirmHold"
                    :cursor-spacing="cursorSpacing"
                    :adjust-position="adjustPosition"
                    :password="password || type === 'password'"

                    @blur="onBlur"
                    @focus="onFocus"
                    @confirm="onConfirm"
            />
            <span
                    v-if="showClear"
                    size="16px"
                    name="clear"
                    class="jst-field__clear-root jst-field__icon-root iconfont icon-cuowu"
                    @click="onClear"
            ></span>
            <div class="jst-field__icon-container" @click="onClickIcon">
                <span
                        v-if="rightIcon || icon"
                        size="16px"
                        :name="rightIcon || icon"
                        :class="'right-icon-class jst-field__icon-root ' + iconClass"
                ></span>
                <slot name="right-icon" />
                <slot name="icon" />
            </div>
            <div class="jst-field__button">
                <slot name="button" />
            </div>
        </div>
        <div v-if="errorMessage" :class="'jst-field__error-message ' + errClass">
            {{ errorMessage }}
        </div>
    </jst-cell>
</template>

<script>
    import link from './../mixins/link';
    import utils from './../tool/utils'
    import { isObj } from './../tool/tools'

    export default {
        name: "jst-field",
        props: {
            size: String,
            icon: String,
            label: String,
            error: Boolean,
            fixed: Boolean,
            focus: Boolean,
            center: Boolean,
            isLink: Boolean,
            leftIcon: String,
            rightIcon: String,
            disabled: Boolean,
            // autosize: Boolean,
            autosize:[Boolean,Object],
            readonly: Boolean,
            required: Boolean,
            password: Boolean,
            iconClass: String,
            clearable: Boolean,
            inputAlign: String,
            customClass: String,
            customStyle: String,
            confirmType: String,
            confirmHold: Boolean,
            errorMessage: String,
            placeholder: String,
            placeholderStyle: String,
            errorMessageAlign: String,
            showConfirmBar: {
                type: Boolean,
                default: true
            },
            adjustPosition: {
                type: Boolean,
                default: true
            },
            cursorSpacing: {
                type: Number,
                default: 50
            },
            maxlength: {
                type: Number,
                default: -1
            },
            type: {
                type: String,
                default: 'text'
            },
            border: {
                type: Boolean,
                default: true
            },
            titleWidth: {
                type: String,
                default: '90px'
            },
            value: {
                type: String,
                default: ''
            }
        },

        data(){
            return {
                showClear: false,
                fieldValue: ""
            }
        },

        watch: {
            fieldValue() {
                this.$nextTick(this.adjustSize);
                this.$emit("input", this.fieldValue);
                this.$emit("change", this.fieldValue);
            },
            value(){
                this.fieldValue = this.value;
            }
        },

        computed: {
            inputClass(){
                return utils.bem('field__input', [this.inputAlign, { disabled: this.disabled, error: this.error }]);
            },
            errClass(){
                return utils.bem('field__error', [this.errorMessageAlign, { disabled: this.disabled, error: this.error }]);
            },
            placeholderClass(){
                return utils.bem('field__placeholder', { error: this.error });
            }
        },

        beforeCreate() {
            // this.focused = false;
            // this.fieldValue = this.value;
        },

        created(){
          this.$nextTick(()=>{
              this.focused = false;
              this.fieldValue = this.value;
              this.adjustSize();
          })
        },

        methods: {
            onInput($event) {
                let fieldValue = this.fieldValue || '';
                this.showClear = this.getShowClear(fieldValue);
                this.emitChange(fieldValue);
                this.fieldValue = $event.target.fieldValue;
            },

            onFocus() {
                let fieldValue = this.fieldValue || '';
                let height = this.height || '';
                this.$emit('focus', { fieldValue, height });
                this.focused = true;
                this.blurFromClear = false;
                this.showClear = this.getShowClear();
            },

            onBlur() {
                let fieldValue = this.fieldValue || '';
                let cursor = this.cursor || 0;
                this.$emit('blur', { fieldValue, cursor });
                this.focused = false;
                const showClear = this.getShowClear();

                if (this.fieldValue === fieldValue) {
                    setTimeout(() => {
                        this.showClear = showClear;
                    }, 50); // blur 和 click 事件冲突 加个定时器
                } else if (!this.blurFromClear) {
                    this.fieldValue = fieldValue;
                    this.showClear = showClear;
                    this.emitChange(fieldValue);
                }
            },

            onClickIcon() {
                this.$emit('click-icon');
            },

            getShowClear(fieldValue) {
                fieldValue = fieldValue === undefined ? this.fieldValue : fieldValue;
                return (
                    this.clearable && this.focused && fieldValue && !this.readonly
                );
            },

            onClear() {
                this.blurFromClear = true;
                console.log("clear")
                this.fieldValue = '';
                this.showClear = this.getShowClear('');
                this.emitChange('');
                this.$emit('clear', '');
            },

            onConfirm() {
                this.$emit('confirm', this.fieldValue);
            },

            emitChange(fieldValue) {
                this.$emit('input', fieldValue);
                this.$emit('change', fieldValue);
            },

            adjustSize() {
                // const  { input }  = this.$refs;
                let input = document.querySelector("textarea");

                if (!(this.type === 'textarea' && this.autosize) || !input) {
                    return;
                }
                // console.log(input);
                // console.log(this.autosize);
                // input.style.height = 'auto';
                input.style.height = '24px';

                let height = input.scrollHeight;
                if (isObj(this.autosize)) {
                    const { maxHeight, minHeight } = this.autosize;
                    if (maxHeight) {
                        height = Math.min(height, maxHeight);
                    }
                    if (minHeight) {
                        height = Math.max(height, minHeight);
                    }
                }

                if (height) {
                    input.style.height = height + 'px';
                }
            },

        }

    }
</script>

<style scoped>

