<template>
    <!--<div-->
            <!--class="jst-picker-column custom-class"-->
            <!--:style="`height: ${ itemHeight * visibleItemCount }px`"-->
            <!--@touchstart="onTouchStart"-->
            <!--@touchmove="onTouchMove"-->
            <!--@touchend="onTouchEnd"-->
            <!--@touchcancel="onTouchEnd"-->
    <!--&gt;-->
        <!--<div :style="wrapperStyle">-->
            <!--<div-->
                    <!--v-for="(option, index) in options"-->
                    <!--:key="index"-->
                    <!--:data-index="index"-->
                    <!--:style="`height: ${ itemHeight }px`"-->
                    <!--:class="`jst-ellipsis jst-picker-column__item ${ option && option.disabled ? 'jst-picker-column__item&#45;&#45;disabled' : '' } ${ index === currentIndex ? 'jst-picker-column__item&#45;&#45;selected active-class' : '' }`"-->
                    <!--@click="onClickItem"-->
            <!--&gt;{{ getOptionText(option, valueKey) }}</div>-->
        <!--</div>-->
    <!--</div>-->

    <div
            :style="columnStyle"
            :class="['jst-picker-column', className]"
            @touchstart="onTouchStart"
            @touchmove="onTouchMove"
            @touchend="onTouchEnd"
            @touchcancel="onTouchEnd"
    >
        <ul :style="wrapperStyle">
            <!--onClick={() => {-->
            <!--this.setIndex(index, true);-->
            <!--}}-->
            <li
                    v-for="(option, index) in options"
                    :key="index"
                    :style="{ height: `${itemHeight}px` }"
                    :class="[
                        'jst-ellipsis',
                        bem('picker-column__item', {
                        disabled: isDisabled(option),
                        selected: index === currentIndex
                        })
                    ]"
                v-html="getOptionText(option)"
                @click="setIndex(index, true)"
            ></li>
        </ul>
    </div>
</template>

<script>

    import { isObj, range, deepClone } from '../tool/tools';
    import utils from './../tool/utils'

    const DEFAULT_DURATION = 200;

    export default {
        name: "jst-picker-column",

        props: {
            valueKey: String,
            className: String,
            itemHeight: Number,
            defaultIndex: Number,
            initialOptions: Array,
            visibleItemCount: Number
        },

        data() {
            return {
                startY: 0,
                offset: 0,
                duration: 0,
                startOffset: 0,
                options: deepClone(this.initialOptions),
                currentIndex: this.defaultIndex
            };
        },

        created() {
            this.$parent.children && this.$parent.children.push(this);
            this.setIndex(this.currentIndex);
        },

        destroyed() {
            const { children } = this.$parent;
            children && children.splice(children.indexOf(this), 1);
        },

        watch: {
            defaultIndex() {
                this.setIndex(this.defaultIndex);
            }
        },

        computed: {
            count() {
                return this.options.length;
            },

            columnStyle(){
                return {
                    height: this.itemHeight * this.visibleItemCount + 'px'
                };
            },

            wrapperStyle(){
                const baseOffset = (this.itemHeight * (this.visibleItemCount - 1)) / 2;
                //console.log(baseOffset);

                const wrapperStyle = {
                    transition: `${this.duration}ms`,
                    transform: `translate3d(0, ${this.offset + baseOffset}px, 0)`,
                    lineHeight: `${this.itemHeight}px`
                };
                return wrapperStyle
            }
        },

        methods: {
            bem: utils.bem,
            onTouchStart(event) {
                this.startY = event.touches[0].clientY;
                this.startOffset = this.offset;
                this.duration = 0;
            },

            onTouchMove(event) {
                event.preventDefault();
                const deltaY = event.touches[0].clientY - this.startY;
                this.offset = range(
                    this.startOffset + deltaY,
                    -(this.count * this.itemHeight),
                    this.itemHeight
                );
            },

            onTouchEnd() {
                if (this.offset !== this.startOffset) {
                    this.duration = DEFAULT_DURATION;
                    const index = range(Math.round(-this.offset / this.itemHeight), 0, this.count - 1);
                    this.setIndex(index, true);
                }
            },

            adjustIndex(index) {
                index = range(index, 0, this.count);
                for (let i = index; i < this.count; i++) {
                    if (!this.isDisabled(this.options[i])) return i;
                }
                for (let i = index - 1; i >= 0; i--) {
                    if (!this.isDisabled(this.options[i])) return i;
                }
            },

            isDisabled(option) {
                return isObj(option) && option.disabled;
            },

            getOptionText(option) {
                return isObj(option) && this.valueKey in option ? option[this.valueKey] : option;
            },

            setIndex(index, userAction) {
                index = this.adjustIndex(index) || 0;
                this.offset = -index * this.itemHeight;

                if (index !== this.currentIndex) {
                    this.currentIndex = index;
                    userAction && this.$emit('change', index);
                }
            },

            setValue(value) {
                const { options } = this;
                for (let i = 0; i < options.length; i++) {
                    if (this.getOptionText(options[i]) === value) {
                        return this.setIndex(i);
                    }
                }
            },

            getValue() {
                return this.options[this.currentIndex];
            }
        },
    }
</script>

