<template>
    <div class="jst-radio" @click="radioClick($event)">
        <div
                :class="bem('radio__icon', [shape, { disabled: isDisabled, checked: checked }])"
                @click="onClickIcon">
            <slot name="icon">
                <jst-icon class="jst-icon" name="success" :style="iconStyle" />
            </slot>
        </div>
        <span
                :class="bem('radio__label', [labelPosition, { disabled: isDisabled }])"
                @click="onClickLabel"
        >
            <slot></slot>
        </span>
    </div>
</template>

<script>

    import utils from './../tool/utils'
    import { CheckboxMixin } from '../mixins/checkbox';
    export default {
        name: "jst-radio",
        mixins: [CheckboxMixin('jst-radio-group', utils.bem)],

        computed: {
            currentValue: {
                get() {
                    return this.parent ? this.parent.value : this.value;
                },

                set(val) {
                    (this.parent || this).$emit('input', val);
                }
            },

            checked() {
                return this.currentValue === this.name;
            }
        },

        methods: {
            onClickIcon() {
                if (!this.isDisabled) {
                    this.currentValue = this.name;
                }
            },
            onClickLabel() {
                if (!this.isDisabled && !this.labelDisabled) {
                    this.currentValue = this.name;
                }
            },
            radioClick(event){
                event.stopPropagation();
                this.$emit('click', event);
            },
            suffixPx: utils.suffixPx,
            bem: utils.bem
        }
    }
</script>

