<template>
    <div
            :style="{ zIndex: zIndex }"
            :class="[
                'jst-hairline--top-bottom',
                bem('tabbar', [{
                    fixed: fixed,
                    'safe-area-inset-bottom': safeAreaInsetBottom
                }])
            ]"
    >
        <slot></slot>
    </div>
</template>

<script>
    import utils from "./../tool/utils";
    export default {
        name: "jst-tabbar",

        data() {
            return {
                items: []
            };
        },

        props: {
            value: Number,
            activeColor: String,
            safeAreaInsetBottom: Boolean,
            fixed: {
                type: Boolean,
                default: true
            },
            zIndex: {
                type: Number,
                default: 1
            }
        },

        watch: {
            items() {
                this.setActiveItem();
            },

            value() {
                this.setActiveItem();
            }
        },

        methods: {
            bem: utils.bem,
            setActiveItem() {
                this.items.forEach((item, index) => {
                    item.active = index === this.value;
                });
            },

            onChange(active) {
                console.log(active)
                if (active !== this.value) {
                    this.$emit('input', active);
                    this.$emit('change', active);
                }
            }
        },

    }
</script>

