<template>
    <div :class="['jst-hairline', bem('step', [$parent.direction, { [status]: status }])]">
        <div :class="bem('step__title')" :style="status === 'process' && { color: $parent.activeColor }" >
            <slot></slot>
        </div>
        <div :class="bem('step__circle-container')">
            <template v-if="status === 'process'">
                <slot name="active-icon">
                    <jst-icon :class="bem('step__icon')" :name="$parent.activeIcon" :color="$parent.activeColor" ></jst-icon>
                </slot>
            </template>

            <template v-else-if="$parent.inactiveIcon">
                <slot name="inactive-icon">
                    <jst-icon :class="bem('step__icon')" :name="$parent.inactiveIcon" ></jst-icon>
                </slot>
            </template>

            <template v-else>
                <i :class="bem('step__circle')" ></i>
            </template>


        </div>
        <div :class="bem('step__line')" ></div>
    </div>
</template>

<script>
    import utils from "./../tool/utils";

    export default {
        name: "jst-step",

        beforeCreate() {
            const { steps } = this.$parent;
            const index = this.$parent.slots().indexOf(this.$vnode);
            steps.splice(index === -1 ? steps.length : index, 0, this);
        },

        beforeDestroy() {
            const index = this.$parent.steps.indexOf(this);
            if (index > -1) {
                this.$parent.steps.splice(index, 1);
            }
        },

        computed: {
            status() {
                const index = this.$parent.steps.indexOf(this);
                const { active } = this.$parent;

                if (index < active) {
                    return 'finish';
                }
                if (index === active) {
                    return 'process';
                }
            }
        },

        methods: {
            bem: utils.bem
        }


    }
</script>

