<template>
    <div v-show="selected || parent.animated" class="jst-tab__pane">
        <div ref="title">
            <slot name="title"></slot>
        </div>
        <!--<template v-if="inited || $parent.lazyRender">-->
            <!--<slot></slot>-->
        <!--</template>-->
        <slot v-if="inited || parent.lazyRender" ></slot>
    </div>
</template>

<script>
    import { FindParentMixin } from '../mixins/find-parent';
    import { SlotsMixin } from '../mixins/slots';
    import utils from '../tool/utils';

    export default {
        name: "jst-tab",

        mixins: [FindParentMixin, SlotsMixin],

        props: {
            title: String,
            disabled: Boolean
        },

        data() {
            return {
                inited: false
            };
        },

        computed: {
            index() {
                return this.parent.tabs.indexOf(this);
            },

            selected() {
                return this.index === this.parent.curActive;
            }
        },

        watch: {
            'parent.curActive'() {
                this.inited = this.inited || this.selected;
            },

            title() {
                this.parent.setLine();
            }
        },

        created() {
            this.findParent('jst-tabs');
        },

        mounted() {
            const { tabs } = this.parent;
            const index = this.parent.slots().indexOf(this.$vnode);
            tabs.splice(index === -1 ? tabs.length : index, 0, this);

            if (this.slots('title')) {
                this.$nextTick(()=>{
                    //console.log(this.$refs, "ada")
                    this.parent.renderTitle(this.$refs.title, this.index);
                })

            }
        },

        beforeDestroy() {
            this.parent.tabs.splice(this.index, 1);
        },


    }
</script>

