<template>
    <div>
        <div
                :class="classes"
                :style="((checked ? activeColor : inactiveColor) ? 'background-color: ' + (checked ? activeColor : inactiveColor ) : '')+ ';' + 'font-size:' + size"
                @click="onClick"
        >
            <div class="jst-switch__node node-class">
                <jst-loading v-if="loading" size="50%" custom-class="jst-switch__loading" />
            </div>
        </div>
    </div>
</template>

<script>
    import utils from './../tool/utils'
    export default {
        name: "jst-switchs",
        data(){
            return{
                value: null
            }
        },
        computed: {
            classes: function(){
                return 'custom-class ' + utils.bem('switch', { on: this.value === this.activeValue, disabled: this.disabled });
            }
        },
        props: {
            checked: null,
            loading: Boolean,
            disabled: Boolean,
            activeColor: String,
            inactiveColor: String,
            size: {
                type: String,
                default: '30px'
            },
            activeValue: {
                type: null,
                default: true
            },
            inactiveValue: {
                type: null,
                default: false
            },
            inactiveColor: String,
        },

        watch: {
            checked(value) {
                this.value = value;
            }
        },

        created() {
            this.value = this.checked;
        },

        methods: {
            onClick() {
                if (!this.disabled && !this.loading) {
                    const checked = this.checked === this.activeValue;
                    const value = checked ? this.inactiveValue : this.activeValue;
                    this.$emit('input', value);
                    this.$emit('change', value);
                }
            }
        }
    }
</script>

