<template>
    <div
            :class="bem('rate')"
            @touchMove="onTouchMove"
    >
        <div
            :class="bem('rate__item')"
            v-for="index in count"
            :key="index"
            :style="'font-size:'+ size + 'px'"
        >
            <jst-icon
                :class="bem('rate__icon')"
                :name="(value >= index ? icon : voidIcon)"
                :style="'font-size:'+ size + 'px'"
                :data-score="index"
                :color="disabled ? disabledColor : (value >= index ? color : voidColor)"
                @click="onSelect(index)"

            />
            <!--<jst-icon-->
                <!--v-if="allowHalf"-->
                <!--:class="bem('rate__icon','half')"-->
                <!--:name="((value < index && value + 0.5 < index) ? voidIcon : icon)"-->
                <!--:style="'font-size:'+ size + 'px'"-->
                <!--:data-score="index"-->
                <!--:color="disabled ? disabledColor : ((value < index && value + 0.5 < index) ? voidIcon : icon)"-->
                <!--@click="onSelect(index)"-->
            <!--/>-->
        </div>
    </div>
</template>

<script>
    import utils from "./../tool/utils";
    import { emit, inherit } from './../tool/functional';

    export default {
        name: "jst-rate",
        field: true,
        props:{
            size: {
                type: Number,
                default: 20
            },                                 //图标大小 (px)
            icon: {
                type: String,
                default: 'icon-pingjia'
            },                                  //选中时的图标名称
            count: {
                type: Number,
                default: 5
            },                                  //图标总数
            color: {
                type: String,
                default: '#f08f00'
            },                                  //选中时的颜色
            value: Number,                    //当前分值
            voidIcon: {
                type: String,
                default: 'icon-shoucang'     //未选中时的图标名称
            },
            voidColor: {
                type: String,
                default: '#c7c7c7'
            },                                  //未选中时的颜色
            readonly: Boolean,                //是否为只读状态
            disabled: Boolean,                //是否禁用评分
            allowHalf: Boolean,               //是否允许半选
            disabledColor: {
                type: String,
                default: '#bdbdbd'
            },                                  //禁用时的颜色
        },

        data(){
            return{
                innerValue: 0
            }
        },

        computed:{
        },

        watch: {
            value(val) {
                if (val !== this.innerValue) {
                    this.innerValue = val;
                }
            },
        },

        methods:{
            onSelect(index){
                if (!this.disabled && !this.readonly) {
                    // this.innerValue = index;
                    this.$emit('input', index);
                    this.$emit('change', index);
                };
            },
            onTouchMove(event) {
                if (this.readonly || this.disabled || !document.elementFromPoint) {
                    return;
                }
                event.preventDefault();


                const { clientX, clientY } = event.touches[0];
                const target = document.elementFromPoint(clientX, clientY);

                if (target && target.dataset) {
                    const { score } = target.dataset;

                    /* istanbul ignore else */
                    if (score) {
                        onSelect(+score);
                    }
                }
            },
            bem: utils.bem
        },

    }
</script>

<style scoped>
    .jst-rate {
        user-select: none;
    }
    .jst-rate__item {
        display: inline-block;
        position: relative;
        padding: 0 2px;
    }
    .jst-rate__icon {
        width: 1em;
    }
    .jst-rate__icon--half {
        position: absolute;
        top: 0;
        left: 2px;
        width: 0.5em;
        overflow: hidden;
    }
</style>
