<template>
    <jst-popup
            :show="show"
            position="bottom"
            :z-index="zIndex"
            :overlay="overlay"
            class="jst-action-sheet"
            :safe-area-inset-bottom="safeAreaInsetBottom"
            :close-on-click-overlay="closeOnClickOverlay"
            @close="onClose"
    >
        <div v-if="title" class="jst-hairline--bottom jst-action-sheet__header">
            {{ title }}
            <jst-icon name="icon-close-circle" class="jst-action-sheet__close" @click="onClose"  />
        </div>
        <div v-if="(actions && actions.length)">
            <div
                    v-for="(item,index) in actions"
                    :key="index"
                    class="jst-hairline--top"
                    :class="bem('action-sheet__item', { disabled: item.disabled || item.loading })"
                    @click="onClose"
            >
                <div v-if="!item.loading"> {{ item.name }}
                    <span v-if="item.subname" class="jst-action-sheet__subname" >{{ item.subname }}</span>
                </div>
                <jst-loading v-else size="20px" />
            </div>
        </div>
        <slot />
        <div v-if="cancelText" class="jst-action-sheet__cancel" @click="onClose"> {{ cancelText }} </div>
    </jst-popup>
</template>

<script>
    import utils from '../tool/utils'
    import { safeArea } from '../mixins/safe-area';

    export default {
        name: "jst-actionsheet",

        mixins: [safeArea()],

        props:{
            show: Boolean,
            title: String,
            cancelText: String,
            zIndex: {
                type: Number,
                default: 100
            },
            actions: {
                type: Array,
                default: ()=>[]
            },
            overlay: {
                type: Boolean,
                default: true
            },
            closeOnClickOverlay: {
                type: Boolean,
                default: true
            }
        },
        methods:{
            // onSelect(event) {
            //     const { index } = event.currentTarget.dataset;
            //     const item = this.actions[index];
            //     if (item && !item.disabled && !item.loading) {
            //         this.$emit('select', item);
            //     }
            // },

            // onCancel() {
            //     this.$emit('cancel');
            // },
            onClose() {
                this.$emit('close');
            },
            bem: utils.bem,
        }
    }
</script>

