<template>
    <div>
        <jst-overlay
                v-if="overlay"
                :mask="mask"
                :show="value"
                :z-index="zIndex"
                :style="overlayStyle"
                @click="onClickOverlay"
                class="jst-image-preview__overlay"
        ></jst-overlay>
        <transition name="jst-fade">
            <div
                    v-show="value"
                    class="jst-image-preview"
                    :class="className"
                    :style="'z-index:' + zIndex"
                    @Touchstart="onWrapperTouchStart"
                    @Touchend="onWrapperTouchEnd"
                    @Touchcancel="onWrapperTouchEnd"
                    @click="onClickOverlay"
            >
                <div v-if="showIndex" class="jst-image-preview__index">
                    <slot v-bind:index="active">
                        {{ active + 1}}/{{images.length }}
                    </slot>
                </div>

                <jst-swipe
                        ref="swipe"
                        :loop="loop"
                        indicatorColor="white"
                        :initialSwipe="startPosition"
                        :showIndicators="showIndicators"
                        @change="onChange"
                >
                    <jst-swipe-item
                            v-for="(image,index) in images"
                            :key="index"
                    >
                        <img
                            v-if="lazyLoad"
                            v-lazy="image"
                            class="jst-image-preview__image"
                            :style="(index === active ? imageStyle : null)"
                            @touchstart="onTouchStart"
                            @touchmove="onTouchMove"
                            @touchend="onTouchEnd"
                            @touchcancel="onTouchEnd"
                        />
                        <img
                                v-else
                                class="jst-image-preview__image"
                                :style="(index === active ? imageStyle : null)"
                                @touchstart="onTouchStart"
                                @touchmove="onTouchMove"
                                @touchend="onTouchEnd"
                                @touchcancel="onTouchEnd"
                                :src="images[index]"
                        />

                    </jst-swipe-item>
                </jst-swipe>
            </div>
        </transition>
    </div>
</template>

<script>
    import { range } from './../tool/tools';
    import { PopupMixin } from './../mixins/popup';
    import { TouchMixin } from './../mixins/touch';

    // PopupMixin

    function getDistance(touches) {
        return Math.sqrt(
            Math.abs(
                (touches[0].clientX - touches[1].clientX) *
                (touches[0].clientY - touches[1].clientY)
            )
        );
    }

    export default {
        mixins: [TouchMixin],

        name: "jst-image-preview",

        props: {
            value: {
                type: Boolean,
                default: false,
            },
            mask: {
                type: Boolean,
                default: true,
            },
            zIndex: {
                type: Number,
                default: 1000
            },
            className: null,
            showIndex: {
                type: Boolean,
                default: true
            },
            images: {
                type: Array,
                default: () =>[]
            },
            loop: {
                type: Boolean,
                default: true
            },
            startPosition: Number,
            showIndicators: Boolean,
            lazyLoad: Boolean,
            asyncClose: Boolean,
            overlay: {
                type: Boolean,
                default: true
            },
            minZoom: {
                type: Number,
                default: 1 / 3
            },
            maxZoom: {
                type: Number,
                default: 3
            },
            overlayClass: {
                type: String,
                default: 'jst-image-preview__overlay'
            },
            overlayStyle: {
              type: String,
              default: ''
            },
            closeOnClickOverlay: {
                type: Boolean,
                default: true
            },
        },

        data() {
            return {
                scale: 1,
                moveX: 0,
                moveY: 0,
                moving: false,
                zooming: false,
                active: 0
            };
        },

        computed: {

            imageStyle() {
                const { scale } = this;
                const style = {
                    transition: this.zooming || this.moving ? '' : '.3s all'
                };

                if (scale !== 1) {
                    style.transform = `scale3d(${scale}, ${scale}, 1) translate(${this.moveX /
                    scale}px, ${this.moveY / scale}px)`;
                }

                return style;
            }
        },

        watch: {
            value() {
                //this.active = this.startPosition;
                // console.log(this.startPosition)
            },

            startPosition(active) {
                this.active = active;
                //console.log(this.active)
            },


        },

        methods: {
            onWrapperTouchStart() {
                this.touchStartTime = new Date();
            },

            onWrapperTouchEnd(event) {
                event.preventDefault();

                const deltaTime = new Date() - this.touchStartTime;
                const { offsetX = 0, offsetY = 0 } = this.$refs.swipe || {};

                // prevent long tap to close component
                if (deltaTime < 300 && offsetX < 10 && offsetY < 10) {
                    const index = this.active;

                    this.resetScale();
                    this.$emit('close', {
                        index,
                        url: this.images[index]
                    });

                    if (!this.asyncClose) {
                        this.$emit('input', false);
                    }
                }
            },

            startMove(event) {
                const image = event.currentTarget;
                const rect = image.getBoundingClientRect(); //返回元素的大小及其相对于视口的位置。
                const winWidth = window.innerWidth;
                const winHeight = window.innerHeight;

                this.touchStart(event);
                this.moving = true;
                this.startMoveX = this.moveX;
                this.startMoveY = this.moveY;
                this.maxMoveX = Math.max(0, (rect.width - winWidth) / 2);
                this.maxMoveY = Math.max(0, (rect.height - winHeight) / 2);
            },

            startZoom(event) {
                this.moving = false;
                this.zooming = true;
                this.startScale = this.scale;
                this.startDistance = getDistance(event.touches);
            },

            onTouchStart(event) {
                const { touches } = event;
                const { offsetX = 0 } = this.$refs.swipe || {};

                if (touches.length === 1 && this.scale !== 1) {
                    this.startMove(event);
                } /* istanbul ignore else */ else if (touches.length === 2 && !offsetX) {
                    this.startZoom(event);
                }
            },

            onTouchMove(event) {
                const { touches } = event;
                if (this.moving || this.zooming) {
                    event.preventDefault();
                    event.stopPropagation();
                }

                if (this.moving) {
                    this.touchMove(event);
                    const moveX = this.deltaX + this.startMoveX;
                    const moveY = this.deltaY + this.startMoveY;
                    this.moveX = range(moveX, -this.maxMoveX, this.maxMoveX);
                    this.moveY = range(moveY, -this.maxMoveY, this.maxMoveY);
                }

                if (this.zooming && touches.length === 2) {
                    const distance = getDistance(touches);
                    const scale = (this.startScale * distance) / this.startDistance;
                    this.scale = range(scale, this.minZoom, this.maxZoom);
                }
            },

            onTouchEnd(event) {
                /* istanbul ignore else */
                if (this.moving || this.zooming) {
                    let stopPropagation = true;

                    if (
                        this.moving &&
                        this.startMoveX === this.moveX &&
                        this.startMoveY === this.moveY
                    ) {
                        stopPropagation = false;
                    }

                    if (!event.touches.length) {
                        this.moving = false;
                        this.zooming = false;
                        this.startMoveX = 0;
                        this.startMoveY = 0;
                        this.startScale = 1;

                        if (this.scale < 1) {
                            this.resetScale();
                        }
                    }

                    if (stopPropagation) {
                        event.preventDefault();
                        event.stopPropagation();
                    }
                }
            },

            onChange(active) {
                this.resetScale();
                this.active = active;
                //console.log(active);
                this.$emit('change', active);
            },

            resetScale() {
                this.scale = 1;
                this.moveX = 0;
                this.moveY = 0;
            },

            onClickOverlay() {
                this.$emit('click-overlay');

                if (this.closeOnClickOverlay) {
                    this.$emit('close');
                    this.$emit('input', false);
                };
            },
        },
    }
</script>

<style scoped>
    .jst-image-preview {
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        position: fixed;
    }
    .jst-image-preview__image {
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        max-width: 100%;
        max-height: 100%;
        position: absolute;
    }
    .jst-image-preview__index {
        position: absolute;
        top: 10px;
        left: 50%;
        color: #fff;
        font-size: 14px;
        letter-spacing: 2px;
        transform: translate(-50%, 0);
    }
    .jst-image-preview__overlay {
        background-color: rgba(0, 0, 0, 0.9);
    }
    .jst-image-preview .jst-swipe {
        height: 100%;
    }
</style>
