<template>
    <div class="jst-progress">
        <span :class="bem('progress__portion', { 'with-pivot': showPivot })" :style="portionStyle">
            <span v-if="showPivot" ref="pivot" :style="pivotStyle" :class="bem('progress__pivot')">
              {{text}}
            </span>
        </span>
    </div>
</template>

<script>
    import { isDef } from '../tool/tools';
    import utils from './../tool/utils';
    import { BLUE, WHITE } from '../tool/color';
    export default {
        name: "jst-progress",
        data(){
            return{
                pivotWidth: 0,
                progressWidth: 0
            }
        },
        props: {
            inactive: Boolean,
            pivotText: String,
            pivotColor: String,
            percentage: {
                type: Number,
                required: true,
                validator: value => value >= 0 && value <= 100
            },
            showPivot: {
                type: Boolean,
                default: true
            },
            color: {
                type: String,
                default: '#3aa1ff'
            },
            textColor: {
                type: String,
                default: WHITE
            }
        },
        computed: {
            pivotStyle: function(){
                const background = this.inactive ? '#c3c3c3' : this.color;
                return {
                    'color': this.textColor,
                    'background' : this.pivotColor || background
                };
            },
            portionStyle: function(){
                return {
                    'background' : this.inactive ? '#c3c3c3' : this.color,
                    'width' : ((this.progressWidth - this.pivotWidth) * this.percentage) / 100 + 'px'
                };
            },
            text: function () {
                return isDef(this.pivotText) ? this.pivotText : this.percentage + '%';
            },
        },
        mounted() {
            this.getWidth();
        },

        watch: {
            showPivot() {
                this.getWidth();
            },

            pivotText() {
                this.getWidth();
            }
        },

        methods: {
            getWidth() {
                this.progressWidth = this.$el.offsetWidth;
                this.pivotWidth = this.$refs.pivot ? this.$refs.pivot.offsetWidth : 0;
            },
            bem: utils.bem
        },
    }
</script>

<style scoped>
    .jst-progress {
        height: 4px;
        position: relative;
        border-radius: 4px;
        background: #e5e5e5;
        margin: 15px;
    }
    .jst-progress__portion {
        left: 0;
        height: 100%;
        position: absolute;
        border-radius: inherit;
    }
    .jst-progress__portion--with-pivot {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
    }
    .jst-progress__pivot {
        top: 50%;
        right: 0;
        min-width: 2em;
        padding: 0 8px;
        font-size: 10px;
        position: absolute;
        line-height: 1.6;
        text-align: center;
        border-radius: 1em;
        word-break: keep-all;
        box-sizing: border-box;
        background-color: #e5e5e5;
        transform: translate(100%, -50%);
    }
</style>
