<template>
    <div>
        <jst-overlay
                v-if="overlay"
                mask
                :show="show"
                :z-index="zIndex"
                :style="overlayStyle"
                @click="onClickOverlay"
        ></jst-overlay>
        <transition :name="transitionName" @transitionend="onTransitionEnd">
            <div
                    v-if="show"
                    :class="customClass + ' ' + popupClass"
                    :style="'z-index:' + zIndex + ';' + customStyle"
            >
                <div v-if="safeAreaInsetTop" class="jst-popup__safe-top" :style="'padding-top:' + statusBarHeight + 'px;'"></div>
                <slot />
            </div>
        </transition>
    </div>
</template>

<script>
    // import { transition } from '../mixins/transition';
    import { safeArea } from '../mixins/safe-area';
    import utils from './../tool/utils'

    export default {
        name: "jst-popup",

        mixins: [safeArea()],

        props: {
            customStyle: String,
            customClass: String,
            overlayStyle: String,
            zIndex: {
                type: Number,
                default: 100
            },
            overlay: {
                type: Boolean,
                default: true
            },
            closeOnClickOverlay: {
                type: Boolean,
                default: true
            },
            position: {
                type: String,
                default: 'center',
            },
            show: {
                type: Boolean,
                default: false,
            },
        },

        computed: {
            popupClass(){
                return utils.bem('popup', [this.position, { safe: this.isIPhoneX && this.safeAreaInsetBottom }])
            },
            transitionName(){
                let obj = {
                    "top": "jst-top",
                    "bottom": "jst-bottom",
                    "left": "jst-left",
                    "right": "jst-right",
                    "center": "jst-center"
                };
                //console.log(obj[this.position])
                return obj[this.position] ? obj[this.position] : obj['center'];
            }
        },

        data(){
            return {
                //display: false,
            }
        },

        created() {

        },

        methods: {
            onClickOverlay() {
                this.$emit('click-overlay');

                if (this.closeOnClickOverlay) {
                    this.$emit('close');
                }
            },
            onTransitionEnd() {
                if (!this.show) {
                    //this.set({ display: false });
                    //this.display = false;
                    this.$emit('transitionEnd');
                }
            }
        }
    }
</script>

