<template>
    <div class="jst-radio-group">
        <slot />
    </div>
</template>

<script>
    import { ParentMixin } from '../mixins/relation';
    export default {
        name: "jst-radio-group",
        props: {
            value: null,
            disabled: Boolean
        },

        watch: {
            value(value) {
                this.$emit('change', value);
            }
        },
    }
</script>

