    <div
            v-if="info"
            :class="'jst-info ' + customClass"
            :style="customStyle"
    >{{ info }}</div>
</template>

<script>
    export default {
        name: "jst-info",

        props: {
            info: null,
            customStyle: {
                type: String,
                default: ''
