<template>
    <ul :class="bem('pagination', [{ simple:  simple}])">
        <li
                :class="[bem('pagination__item', { disabled: value === 1 }), bem('pagination__prev'), 'jst-hairline']"
                @click="onSelect(value - 1)"
        >
            {{prevText || "上一页"}}
        </li>
        <li
                v-for="(page, index) in pages"
                :key="index"
                :class="[bem('pagination__item', { active: page.active }), bem('pagination__page'), 'jst-hairline']"
                @click="onSelect(page.number)"
        >
            {{ page.text }}
        </li>
        <li v-if="simple" :class="bem('pagination__page-desc')">
            <slot name="pageDesc">
                {{`${value}/${count}` }}
            </slot>
        </li>
        <li
                :class="[bem('pagination__item', { disabled: value === count }), bem('pagination__next'), 'jst-hairline']"
                @click="onSelect(value + 1)"
        >
            {{this.nextText || "下一页"}}
        </li>
    </ul>
</template>

<script>
    import utils from "./../tool/utils";

    function makePage(number, text, active) {
        return { number, text, active };
    }

    export default {
        name: "jst-pagination",

        props: {
            value: Number,
            prevText: String,
            nextText: String,
            pageCount: Number,
            totalItems: Number,
            forceEllipses: Boolean,
            mode: {
                type: String,
                default: 'multi'
            },
            itemsPerPage: {
                type: Number,
                default: 10
            },
            showPageSize: {
                type: Number,
                default: 5
            }
        },

        computed: {
            count() {
                const count = this.pageCount || Math.ceil(this.totalItems / this.itemsPerPage);
                return Math.max(1, count);
            },

            pages() {
                const pages = [];
                const pageCount = this.count;

                if (this.mode !== 'multi') {
                    return pages;
                }

                // Default page limits
                let startPage = 1;
                let endPage = pageCount;
                const isMaxSized = this.showPageSize !== undefined && this.showPageSize < pageCount;

                // recompute if showPageSize
                if (isMaxSized) {
                    // Current page is displayed in the middle of the visible ones
                    startPage = Math.max(this.value - Math.floor(this.showPageSize / 2), 1);
                    endPage = startPage + this.showPageSize - 1;

                    // Adjust if limit is exceeded
                    if (endPage > pageCount) {
                        endPage = pageCount;
                        startPage = endPage - this.showPageSize + 1;
                    }
                }

                // Add page number links
                for (let number = startPage; number <= endPage; number++) {
                    const page = makePage(number, number, number === this.value);
                    pages.push(page);
                }

                // Add links to move between page sets
                if (isMaxSized && this.showPageSize > 0 && this.forceEllipses) {
                    if (startPage > 1) {
                        const previousPageSet = makePage(startPage - 1, '...', false);
                        pages.unshift(previousPageSet);
                    }

                    if (endPage < pageCount) {
                        const nextPageSet = makePage(endPage + 1, '...', false);
                        pages.push(nextPageSet);
                    }
                }

                return pages;
            },

            simple(){
                return this.mode !== 'multi';
            }
        },

        watch: {
            value: {
                handler(page) {
                    this.select(page || this.value);
                },
                immediate: true
            }
        },

        methods: {
            bem: utils.bem,
            select(page, emitChange) {
                page = Math.min(this.count, Math.max(1, page));
                if (this.value !== page) {
                    this.$emit('input', page);

                    if (emitChange) {
                        this.$emit('change', page);
                    }
                }
            },
            onSelect(value){
                this.select(value, true);
            }
        },

    }
</script>

