<template>
    <div class="jst-picker">
        <!--{Toolbar}-->

        <div v-if="showToolbar" class="jst-hairline--top-bottom jst-picker__toolbar">
            <div class="jst-picker__cancel" @click="onCancel">
                {{cancelButtonText || 'cancel' }}
            </div>
            <slot name="title">
                <div v-if="title" class="jst-picker__title jst-ellipsis">{{this.title}}</div>
            </slot>

            <div class="jst-picker__confirm" @click="onConfirm">
                {{ confirmButtonText || 'confirm' }}
            </div>
        </div>
        <div v-if="loading" class="jst-picker__loading">
            <jst-loading  color="#1989fa" ></jst-loading>
        </div>

        <div class="jst-picker__columns" :style="{ height: `${itemHeight * this.visibleItemCount}px` }" @touchmove="prevent">
            <jst-picker-column
                    v-for="(item, index) in (simple ? [columns] : columns)"
                    :key="index"
                    :valueKey="valueKey"
                    :className="item.className"
                    :itemHeight="itemHeight"
                    :defaultIndex="item.defaultIndex || defaultIndex"
                    :visibleItemCount="visibleItemCount"
                    :initialOptions="simple ? item : item.values"
                    @change="onChange(index)"
            ></jst-picker-column>
            <div class="jst-hairline--top-bottom jst-picker__frame" :style="{ height: `${itemHeight}px`}" ></div>
        </div>
    </div>
</template>

<script>
    import { pickerProps } from './shared';
    import { deepClone } from '../tool/tools';
    import utils from './../tool/utils';

    export default {
        name: "jst-picker",

        props: {
            ...pickerProps,
            columns: Array,
            defaultIndex: {
                type: Number,
                default: 0
            },
            valueKey: {
                type: String,
                default: 'text'
            }
        },

        data() {
            return {
                children: []
            };
        },

        computed: {
            simple() {
                return this.columns.length && !this.columns[0].values;
            },
            columnsClasses(){
                //return this.bem('columns');
                return utils.bem('picker', ['columns']);
            },
            pickClass(){
                return utils.bem('picker');
            }
        },

        watch: {
            columns() {
                this.setColumns();
            }
        },

        methods: {
            bem: utils.bem,
            setColumns() {
                const columns = this.simple ? [{ values: this.columns }] : this.columns;
                columns.forEach((column, index) => {
                    this.setColumnValues(index, deepClone(column.values));
                });
            },

            emit(event) {
                if (this.simple) {
                    this.$emit(event, this.getColumnValue(0), this.getColumnIndex(0));
                } else {
                    this.$emit(event, this.getValues(), this.getIndexes());
                }
            },

            onChange(columnIndex) {
                if (this.simple) {
                    this.$emit('change', this, this.getColumnValue(0), this.getColumnIndex(0));
                } else {
                    this.$emit('change', this, this.getValues(), columnIndex);
                }
            },

            // get column instance by index
            getColumn(index) {
                return this.children[index];
            },

            // get column value by index
            getColumnValue(index) {
                const column = this.getColumn(index);
                return column && column.getValue();
            },

            // set column value by index
            setColumnValue(index, value) {
                const column = this.getColumn(index);
                column && column.setValue(value);
            },

            // get column option index by column index
            getColumnIndex(columnIndex) {
                return (this.getColumn(columnIndex) || {}).currentIndex;
            },

            // set column option index by column index
            setColumnIndex(columnIndex, optionIndex) {
                const column = this.getColumn(columnIndex);
                column && column.setIndex(optionIndex);
            },

            // get options of column by index
            getColumnValues(index) {
                return (this.children[index] || {}).options;
            },

            // set options of column by index
            setColumnValues(index, options) {
                const column = this.children[index];
                if (column && JSON.stringify(column.options) !== JSON.stringify(options)) {
                    column.options = options;
                    column.setIndex(0);
                }
            },

            // get values of all columns
            getValues() {
                return this.children.map(child => child.getValue());
            },

            // set values of all columns
            setValues(values) {
                values.forEach((value, index) => {
                    this.setColumnValue(index, value);
                });
            },

            // get indexes of all columns
            getIndexes() {
                return this.children.map(child => child.currentIndex);
            },

            // set indexes of all columns
            setIndexes(indexes) {
                indexes.forEach((optionIndex, columnIndex) => {
                    this.setColumnIndex(columnIndex, optionIndex);
                });
            },

            onConfirm() {
                this.emit('confirm');
            },

            onCancel() {
                this.emit('cancel');
            },

            prevent(event){
                event.preventDefault();
            }
        },
    }
</script>

