<template>
    <div>
        <jst-cell
                center
                :title="title"
                :border="border"
                custom-class="jst-switch-cell"
        >
            <jst-switchs
                    :size="size"
                    :checked="checked"
                    :loading="loading"
                    :disabled="disabled"
                    :active-color="activeColor"
                    :inactive-color="inactiveColor"
                    :active-value="activeValue"
                    :inactive-value="inactiveValue"
                    custom-class="jst-switch-cell__switch"
                    @change="onChange"
            />
        </jst-cell>
    </div>
</template>

<script>
    export default {
        name: "jst-switch-cell",
        data(){
            return{
                value: null
            }
        },
        props: {
            title: String,
            border: Boolean,
            checked: Boolean,
            loading: Boolean,
            disabled: Boolean,
            activeColor: String,
            inactiveColor: String,
            size: {
                type: String,
                value: '24px'
            },
            activeValue: {
                type: null,
                value: true
            },
            inactiveValue: {
                type: null,
                value: false
            }
        },

        watch: {
            checked(value) {
                this.value = value;
            }
        },

        created() {
            this.value = this.checked;
        },

        methods: {
            onChange(value) {
                this.$emit('change', value);
            }
        }
    }
</script>

<style scoped>
    .jst-switch-cell {
        padding-top: 9px;
        padding-bottom: 9px;
    }
    .jst-switch-cell__switch {
        vertical-align: middle;
    }
</style>