<template>
    <transition name="jst-fade" >
        <div
                v-show="show"
                class="jst-overlay"
                :style="'z-index:' + zIndex + ';' + (mask ? 'background-color: rgba(0, 0, 0, .7);' : '') + customStyle "
                @click="onClick"
                @touchmove="noop"
        ></div>
    </transition>
</template>

<script>
    export default {
        name: "jst-overlay",

        props: {
            show: Boolean,
            mask: Boolean,
            customStyle: String,
            zIndex: {
                type: Number,
                default: 1
            }
        },

        methods: {
            onClick() {
                this.$emit('click');
            },

            // for prevent touchmove
            noop() {}
        }
    }
</script>

<style scoped>
    .jst-overlay {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }
</style>
