<template>
    <a
            :href="url"
            :class="[bem('badge', [{ select: select }]), 'jst-hairline']"
            @click="onClick"
    >
        <div class="jst-badge__text">
            {{title}}
            <jst-info :info="info" :class="bem('badge__info')" ></jst-info>
        </div>
    </a>
</template>

<script>
    import utils from "./../tool/utils";

    export default {
        name: "jst-badge",

        props: {
            url: String,
            info: [String, Number],
            title: String
        },

        inject: ['jstBadgeGroup'],

        created() {
            this.parent.badges.push(this);
        },

        beforeDestroy() {
            this.parent.badges = this.parent.badges.filter(item => item !== this);
        },

        computed: {
            parent() {
                if (process.env.NODE_ENV !== 'production' && !this.jstBadgeGroup) {
                    console.error('[Vant] Badge needs to be child of BadgeGroup');
                }
                return this.jstBadgeGroup;
            },

            select() {
                return this.parent.badges.indexOf(this) === +this.parent.activeKey;
            }
        },

        methods: {
            bem: utils.bem,
            onClick() {
                const index = this.parent.badges.indexOf(this);
                this.$emit('click', index);
                this.parent.$emit('change', index);
            }
        },

    }
</script>

