<template>
    <div :class="bem('tabbar-item', { active: active })" :style="tabbarItemStyle" @click="onClick">
        <div :class="['jst-tabbar-item__icon', (dot ? 'jst-tabbar-item__icon--dot' : '' )]">
            <slot name="icon" v-bind:active="active" >
                <jst-icon :name="icon"></jst-icon>
            </slot>
            <jst-info :info="info" ></jst-info>
        </div>
        <div class="jst-tabbar-item__text">
            <slot v-bind:active="active" ></slot>
        </div>
    </div>
</template>

<script>
    import { route, routeProps } from "./../tool/router";
    import utils from "./../tool/utils";

    export default {
        name: "jst-tabbar-item",

        props: {
            ...routeProps,
            icon: String,
            dot: Boolean,
            info: [String, Number]
        },

        computed: {
            tabbarItemStyle(){
                return this.active ? { color: this.$parent.activeColor } : null;
            }
        },

        data() {
            return {
                active: false
            };
        },

        beforeCreate() {
            this.$parent.items.push(this);
        },

        destroyed() {
            this.$parent.items.splice(this.$parent.items.indexOf(this), 1);
        },

        methods: {
            bem: utils.bem,
            onClick(event) {
                this.$parent.onChange(this.$parent.items.indexOf(this));
                this.$emit('click', event);
                route(this.$router, this);
            }
        },

    }
</script>

