<template>
    <span
            :style="style"
            :class="[classTag,{ 'jst-hairline--surround': plain }]"
    >
        <slot></slot>
    </span>
</template>

<script>
    import { RED, BLUE, GREEN, GRAY_DARK } from '../tool/color';
    import utils from "./../tool/utils";

    const COLOR_MAP = {
        danger: RED,
        primary: BLUE,
        success: GREEN
    };

    export default {
        name: "jst-tag",

        props: {
            size: String,
            type: String,
            mark: Boolean,
            color: String,
            plain: Boolean,
            round: Boolean,
            textColor: String
        },

        computed: {
            classTag() {
                return utils.bem("tag", [{ mark: this.mark, plain: this.plain, round: this.round, [this.size]: this.size }]);
            },
            style(){
                const { type, plain } = this;
                const color = this.color || (type && COLOR_MAP[type]) || GRAY_DARK;
                const key = plain ? 'color' : 'backgroundColor';
                const t_style = { [key]: color };
                if (this.textColor) {
                    t_style.color = this.textColor;
                }
                return t_style;
            }
        }
    }
</script>

