<template>
    <div :class="['jst-collapse', { 'jst-hairline--top-bottom': border }]">
        <slot></slot>
    </div>
</template>

<script>
    export default {
        name: "jst-collapse",

        props: {
            accordion: Boolean,
            value: [String, Number, Array],
            border: {
                type: Boolean,
                default: true
            }
        },

        data() {
            return {
                items: []
            };
        },

        methods: {
            switch(name, expanded) {
                if (!this.accordion) {
                    name = expanded
                        ? this.value.concat(name)
                        : this.value.filter(activeName => activeName !== name);
                }
                this.$emit('change', name);
                this.$emit('input', name);
            }
        },
    }
</script>

