<template>
    <jst-popup
            :show="value"
            position="top"
            :overlay="overlay"
            @input="onInput"
            @click="onClick"
    >
        <div class="jst-notify" :style="'background-color:'+ background +';'+ 'color:' + color + ';'">
            {{ message }}
        </div>
    </jst-popup>



</template>

<script>
    //import { RED, WHITE } from './../tool/color';
    export default {
        name: "jst-notify",

        props:{
            message: [String, Number],
            className: null,
            color: {
                type: String,
                default: '#fff'
            },
            background: {
                type: String,
                default: "#3aa1ff"
            },
            duration: {
                type: Number,
                default: 3000
            },

            value: {
                type: Boolean,
                default: false
            },
            overlay: {
                type: Boolean,
                default: false
            },
            lockScroll: {
                type: Boolean,
                default: false
            },
            zIndex:{
                type: Number,
                default: 110

            },
        },




        methods:{
            updateZIndex() {
                this.$nextTick(() => {
                    this.$el.style.zIndex = this.zIndex + 1;
                });
            },

            onInput(value){
                this.$emit("input",value)
            },
            onClick(event){
                this.$emit("click",event)
            },

        }

    }
</script>

<style scoped>
    .jst-notify {
        text-align: center;
        box-sizing: border-box;
        font-size: 14px;
        height: 46px;
        line-height: 46px;
    }
</style>

