<template>
    <div :class="bem('steps', [this.direction])">
        <div class="jst-steps__items jst-steps__items--alone">
            <slot></slot>
        </div>
    </div>
</template>

<script>
    import { BLUE } from "./../tool/color";
    import utils from "./../tool/utils";
    import { SlotsMixin } from "./../mixins/slots";

    export default {
        name: "jst-steps",

        mixins: [SlotsMixin],

        props: {
            active: {
                type: Number,
                default: 0
            },
            inactiveIcon: String,
            direction: {
                type: String,
                default: 'horizontal'
            },
            activeColor: {
                type: String,
                default: '#3aa1ff'
            },
            activeIcon: {
                type: String,
                default: 'checkeds'
            }
        },

        data() {
            return {
                steps: []
            };
        },

        methods: {
            bem: utils.bem
        }
    }
</script>

