<template>
    <div class="jst-pull-refresh">
        <div
                class="jst-pull-refresh__track"
                :style="pullRefreshStyle"
                @touchstart="onTouchStart"
                @touchmove="onTouchMove"
                @touchend="onTouchEnd"
                @touchcancel="onTouchEnd"
        >
            <div class="jst-pull-refresh__head">
                <!--{Status}-->
                <slot :name="status">
                    <div v-if="TEXT_STATUS.indexOf(status) !== -1" class="jst-pull-refresh__text">{{text}}</div>
                    <div v-if="status=='loading'" class="jst-pull-refresh__loading">
                        <jst-loading ></jst-loading>
                        <span>{{text}}</span>
                    </div>
                </slot>
            </div>
            <slot></slot>
        </div>
    </div>
</template>

<script>
    import { TouchMixin } from "./../mixins/touch";
    import { getScrollTop, getScrollEventTarget } from './../tool/scroll';
    import { preventDefault } from './../tool/event';
    import utils from './../tool/utils';

    const TEXT_STATUS = ['pulling', 'loosing', 'success'];

    export default {
        name: "jst-pull-refresh",

        mixins: [TouchMixin],

        props: {
            disabled: Boolean,
            successText: String,
            pullingText: String,
            loosingText: String,
            loadingText: String,
            value: {
                type: Boolean,
                required: true
            },
            successDuration: {
                type: Number,
                default: 500
            },
            animationDuration: {
                type: Number,
                default: 300
            },
            headHeight: {
                type: Number,
                default: 50
            }
        },

        data() {
            return {
                status: 'normal',
                height: 0,
                duration: 0,
                TEXT_STATUS: TEXT_STATUS
            };
        },

        computed: {
            untouchable() {
                return this.status === 'loading' || this.status === 'success' || this.disabled;
            },
            pullRefreshStyle(){
                return {
                    transition: `${this.duration}ms`,
                    transform: this.height ? `translate3d(0,${this.height}px, 0)` : ''
                }
            },
            text(){
                let t = {
                    pulling: "下拉即可刷新...",
                    loosing: "释放即可刷新...",
                    loading: "加载中...",
                    success: ""
                };
                //console.log(t[this.status], this.status)
                return this[`${this.status}Text`] || t[this.status]
            }
        },

        watch: {
            value(loading) {
                this.duration = this.animationDuration;

                if (!loading && this.successText) {
                    this.status = 'success';
                    setTimeout(() => {
                        this.setStatus(0);
                    }, this.successDuration);
                } else {
                    this.setStatus(loading ? this.headHeight : 0, loading);
                }
            }
        },

        mounted() {
            this.scrollEl = getScrollEventTarget(this.$el);
        },

        methods: {
            bem: utils.bem,
            onTouchStart(event) {
                if (!this.untouchable && this.getCeiling()) {
                    this.duration = 0;
                    this.touchStart(event);
                }
            },

            onTouchMove(event) {
                if (this.untouchable) {
                    return;
                }

                this.touchMove(event);

                if (!this.ceiling && this.getCeiling()) {
                    this.duration = 0;
                    this.startY = event.touches[0].clientY;
                    this.deltaY = 0;
                }

                if (this.ceiling && this.deltaY >= 0) {
                    if (this.direction === 'vertical') {
                        this.setStatus(this.ease(this.deltaY));
                        preventDefault(event);
                    }
                }
            },

            onTouchEnd() {
                if (!this.untouchable && this.ceiling && this.deltaY) {
                    this.duration = this.animationDuration;
                    if (this.status === 'loosing') {
                        this.setStatus(this.headHeight, true);
                        this.$emit('input', true);
                        this.$emit('refresh');
                    } else {
                        this.setStatus(0);
                    }
                }
            },

            getCeiling() {
                this.ceiling = getScrollTop(this.scrollEl) === 0;
                return this.ceiling;
            },

            ease(height) {
                const { headHeight } = this;
                return height < headHeight
                    ? height
                    : height < headHeight * 2
                        ? Math.round(headHeight + (height - headHeight) / 2)
                        : Math.round(headHeight * 1.5 + (height - headHeight * 2) / 4);
            },

            setStatus(height, isLoading) {
                this.height = height;

                const status = isLoading
                    ? 'loading'
                    : height === 0
                        ? 'normal'
                        : height < this.headHeight
                            ? 'pulling'
                            : 'loosing';

                if (status !== this.status) {
                    this.status = status;
                }
            }
        },

    }
</script>

