<template>
    <div class=jst-list>
        <div v-if="direction === 'down'">
            <slot></slot>
        </div>
        <div v-if="loading">
            <div :class="bem('list__loading')" key="loading">
                <slot>
                    <jst-loading :class="bem('list__loading-icon')" />
                    <span :class="bem('list__loading-text')">{{loadingText || '加载中'}}</span>
                </slot>
            </div>
        </div>
        <div v-if="finished || finishedText">
            <div :class="bem('list__finished-text')">{{finishedText}}</div>
        </div>
        <div v-if="error || errorText">
            <div @click="clickErrorText" :class="bem('list__error-text')">
                {{errorText}}
            </div>
        </div>
        <div v-if="direction === 'up'">
            <slot></slot>
        </div>
    </div>
</template>

<script>
    import Loading from '../loading';
    import utils from './../tool/utils';
    import { on, off } from '../tool/event';
    import {
        getScrollTop,
        getElementTop,
        getVisibleHeight,
        getScrollEventTarget
    } from '../tool/scroll';
    export default {
        name: "jst-list",
        model: {
            prop: 'loading'
        },
        props: {
            error: Boolean,
            loading: Boolean,
            finished: Boolean,
            errorText: String,
            loadingText: String,
            finishedText: String,
            immediateCheck: {
                type: Boolean,
                default: true
            },
            offset: {
                type: Number,
                default: 300
            },
            direction: {
                type: String,
                default: 'down'
            }
        },
        mounted() {
            this.scroller = getScrollEventTarget(this.$el);
            this.handler(true);

            if (this.immediateCheck) {
                this.$nextTick(this.check);
            }
        },
        destroyed() {
            this.handler(false);
        },
        activated() {
            this.handler(true);
        },
        deactivated() {
            this.handler(false);
        },
        watch: {
            loading() {
                this.$nextTick(this.check);
            },

            finished() {
                this.$nextTick(this.check);
            }
        },
        methods: {
            check() {
                if (this.loading || this.finished || this.error) {
                    return;
                }

                const el = this.$el;
                const {scroller} = this;
                const scrollerHeight = getVisibleHeight(scroller);

                /* istanbul ignore next */
                if (
                    !scrollerHeight ||
                    window.getComputedStyle(el).display === 'none' ||
                    el.offsetParent === null
                ) {
                    return;
                }

                const {offset, direction} = this;

                function isReachEdge() {
                    if (el === scroller) {
                        const scrollTop = getScrollTop(el);

                        if (direction === 'up') {
                            return scrollTop <= offset;
                        }

                        const targetBottom = scrollTop + scrollerHeight;
                        return scroller.scrollHeight - targetBottom <= offset;
                    }

                    if (direction === 'up') {
                        return getScrollTop(scroller) - getElementTop(el) <= offset;
                    }

                    const elBottom = getElementTop(el) + getVisibleHeight(el) - getElementTop(scroller);
                    return elBottom - scrollerHeight <= offset;
                }

                if (isReachEdge()) {
                    this.$emit('input', true);
                    this.$emit('load');
                }
            },
            clickErrorText() {
                this.$emit('update:error', false);
                this.$nextTick(this.check);
            },
            handler(bind) {
                /* istanbul ignore else */
                if (this.binded !== bind) {
                    this.binded = bind;
                    (bind ? on : off)(this.scroller, 'scroll', this.check);
                }
            },
            bem: utils.bem
        },
    }
</script>

<style scoped>
    .jst-list__loading-text,
    .jst-list__finished-text,
    .jst-list__error-text {
        color: #969799;
        font-size: 13px;
        line-height: 50px;
        text-align: center;
    }
    .jst-list__loading {
        text-align: center;
    }
    .jst-list__loading-icon,
    .jst-list__loading-text {
        display: inline-block;
        vertical-align: middle;
    }
    .jst-list__loading-icon {
        width: 16px;
        height: 16px;
        margin-right: 5px;
    }

</style>