<template>
    <div>
        <div
                v-show="showNoticeBar"
                :class="wapClass"
                :style="barStyle"
                @click="$emit('click')"
        >
            <slot name="left-icon" v-if="leftIcon">
                <jst-icon :class="bem('notice-bar__left-icon')" :name="leftIcon"></jst-icon>
            </slot>
            <div ref="wrap" :class="bem('notice-bar__wrap')">
                <div
                        ref="content"
                        :class="conClass"
                        :style="contentStyle"
                        @animationend="onAnimationEnd"
                        @webkitAnimationEnd="onAnimationEnd"
                >
                    <slot>{{text}}</slot>
                </div>
            </div>
            <slot name="right-icon"  v-if="mode === 'closeable' ? 'icon-guanbi' : mode === 'link' ? 'arrow' : ''">
                <jst-icon :class="bem('notice-bar__right-icon')" :name="mode === 'closeable' ? 'icon-guanbi' : mode === 'link' ? 'arrow' : ''" @click="onClickIcon"></jst-icon>
            </slot>
        </div>
    </div>
</template>

<script>
    import Icon from '../icon';
    import utils from './../tool/utils';
    export default {
        name: "jst-notice-bar",
        data(){
            return{
                wrapWidth: 0,
                firstRound: true,
                duration: 0,
                offsetWidth: 0,
                showNoticeBar: true,
                animationClass: ''
            }
        },
        props: {
            text: String,
            mode: String,
            color: String,
            leftIcon: String,
            wrapable: Boolean,
            background: String,
            delay: {
                type: [String, Number],
                default: 1
            },
            scrollable: {
                type: Boolean,
                default: true
            },
            speed: {
                type: Number,
                default: 50
            }
        },
        computed: {
            barStyle: function(){
                return {
                    'color': this.color,
                    'background' : this.background
                };
            },
            contentStyle: function(){
                return {
                    'paddingLeft' : this.firstRound ? 0 : this.wrapWidth + 'px',
                    'animationDelay' : (this.firstRound ? this.delay : 0) + 's',
                    'animationDuration' : this.duration + 's',
                };
            },
            conClass: function () {
                return [
                    utils.bem('notice-bar__content'),
                    this.animationClass,
                    !this.scrollable && !this.wrapable ? 'jst-ellipsis' : ''
                ]
            },
            wapClass: function () {
                return utils.bem('notice-bar',{ 'wrapable': this.wrapable })
            }

        },
        watch: {
            text: {
                handler() {
                    this.$nextTick(() => {
                        const { wrap, content } = this.$refs;
                        if (!wrap || !content) {
                            return;
                        }
                        const wrapWidth = wrap.getBoundingClientRect().width;
                        const offsetWidth = content.getBoundingClientRect().width;
                        if (this.scrollable && offsetWidth > wrapWidth) {
                            this.wrapWidth = wrapWidth;
                            this.offsetWidth = offsetWidth;
                            this.duration = offsetWidth / this.speed;
                            this.animationClass = this.bem('notice-bar__play');
                        }
                    });
                },
                immediate: true
            }
        },

        methods: {
            onClickIcon() {
                if (this.mode === 'closeable') {
                    this.showNoticeBar = false;
                    this.$emit('close');
                }
            },
            onAnimationEnd() {
                this.firstRound = false;
                this.$nextTick(() => {
                    this.duration = (this.offsetWidth + this.wrapWidth) / this.speed;
                    this.animationClass = this.bem('notice-bar__play--infinite');
                });
            },
            bem: utils.bem
        },
    }
</script>

<style scoped>
    .jst-notice-bar {
        display: flex;
        height: 40px;
        padding: 0 15px;
        font-size: 14px;
        line-height: 24px;
        position: relative;
        align-items: center;
        color: #ed6a0c;
        background-color: #fffbe8;
        margin-bottom: 10px;
    }
    .jst-notice-bar__left-icon,
    .jst-notice-bar__right-icon {
        font-size: 12px;
        min-width: 22px;
    }
    .jst-notice-bar__right-icon {
        text-align: right;
        color: #999;
    }
    .jst-notice-bar__wrap {
        flex: 1;
        height: 24px;
        overflow: hidden;
        position: relative;
        color: #333;
    }
    .jst-notice-bar__content {
        position: absolute;
        white-space: nowrap;
    }
    .jst-notice-bar__content.jst-ellipsis {
        max-width: 100%;
    }
    .jst-notice-bar__play {
        animation: jst-notice-bar-play linear both;
    }
    .jst-notice-bar__play--infinite {
        animation: jst-notice-bar-play-infinite linear infinite both;
    }
    .jst-notice-bar--wrapable {
        height: auto;
        padding: 8px 15px;
    }
    .jst-notice-bar--wrapable .jst-notice-bar__wrap {
        height: auto;
    }
    .jst-notice-bar--wrapable .jst-notice-bar__content {
        position: relative;
        white-space: normal;
    }
    /**
     * Declare two same keyframes
     * In case that some mobile browsers can continue animation when className changed
     */
    @keyframes jst-notice-bar-play {
        to {
            transform: translate3d(-100%, 0, 0);
        }
    }
    @keyframes jst-notice-bar-play-infinite {
        to {
            transform: translate3d(-100%, 0, 0);
        }
    }

</style>
