<template>
    <div>
        <transition :name="transition ? 'jst-slide-up' : ''">
            <div
                    v-show="show"
                    :style="zIndexsty"
                    :class="'jst-number-keyboard ' + themeClass"
                    @touchstart="stop"
                    @animationend="onAnimationEnd"
                    @webkitAnimationEnd="onAnimationEnd"
            >
                <div v-if="showTitle" :class="bem('number-keyboard__title') + ' jst-hairline--top'">
                    <span v-if="titleLeftSlot" :class="bem('title-left')">
                        <slot class="title-left"></slot>
                    </span>
                    <span v-if="title">{{title}}</span>
                    <span v-if="showTitleClose" :class="bem('number-keyboard__close')" @click="onClose">
                        {{closeButtonText}}
                    </span>
                </div>
                <div class="jst-number-keyboard__body">
                    <jst-keys v-for="key in keys" :key="key.text" :text="key.text" :type="key.type" @press="onPress"/>
                </div>
                <div v-if="theme === 'custom'" class="jst-number-keyboard__sidebar">
                    <jst-keys :text="deleteText" :type="DELETE_KEY_TYPE" @press="onPress"/>
                    <jst-keys :text="closeButtonText" :type="CLOSE_KEY_TYPE" @press="onPress"/>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
    import utils from './../tool/utils'
    import { stop } from '../tool/event';
    import jstKeys from './keys'
    import { SlotsMixin } from '../mixins/slots';
    export default {
        name: "jst-number-keyboard",
        components: {
            jstKeys
        },
        mixins: [ SlotsMixin ],
        data(){
            return{
                CLOSE_KEY_TYPE: ['key--blue '+ 'jst-key--big ' + 'jst-'],
                DELETE_KEY_TYPE: ['key--delete ' + 'jst-key--big ' + 'jst-key--gray ' + 'jst-'],


            }
        },
        props: {
            show: Boolean,
            title: String,
            closeButtonText: String,
            deleteButtonText: String,
            safeAreaInsetBottom: Boolean,
            theme: {
                type: String,
                default: 'default'
            },
            extraKey: {
                type: String,
                default: ''
            },
            zIndex: {
                type: Number,
                default: 100
            },
            transition: {
                type: Boolean,
                default: true
            },
            showDeleteKey: {
                type: Boolean,
                default: true
            },
            hideOnClickOutside: {
                type: Boolean,
                default: true
            }
        },
        mounted() {
            this.handler(true);
        },

        destroyed() {
            this.handler(false);
        },

        activated() {
            this.handler(true);
        },

        deactivated() {
            this.handler(false);
        },

        watch: {
            show() {
                if (!this.transition) {
                    this.$emit(this.show ? 'show' : 'hide');
                }
            }
        },

        computed: {
            keys() {
                const keys = [];
                for (let i = 1; i <= 9; i++) {
                    keys.push({ text: i });
                }

                switch (this.theme) {
                    case 'default':
                        keys.push(
                            { text: this.extraKey, type: ['key--gray '] },
                            { text: 0 },
                            { text: this.deleteText, type: ['key--gray'+ ' ' + 'jst-key--delete '] }
                        );
                        break;
                    case 'custom':
                        keys.push({ text: 0, type: ['key--middle '] },{ text: this.extraKey });
                        break;
                }

                return keys;
            },

            deleteText() {
                return this.deleteButtonText || '删除';
            },
            titleLeftSlot:function(){
               return this.slots('title-left');
            },
            showTitleClose:function () {
                return this.closeButtonText && this.theme === 'default';
            },
            showTitle:function () {
                return this.title || this.showTitleClose || this.titleLeftSlot;
            },
            themeClass:function () {
              return utils.bem(['number-keyboard--' + this.theme + ' ' + { 'safe-area-inset-bottom': this.safeAreaInsetBottom }])
            },
            zIndexsty:function () {
                return {
                    'z-index' : this.zIndex
                }
            },

        },

        methods: {
            handler(action) {
                /* istanbul ignore if */
                if (this.$isServer) {
                    return;
                }

                if (action !== this.handlerStatus && this.hideOnClickOutside) {
                    this.handlerStatus = action;
                    document.body[(action ? 'add' : 'remove') + 'EventListener']('touchstart', this.onBlur);
                }
            },

            onBlur() {
                this.$emit('blur');
            },

            onClose() {
                this.$emit('close');
                this.onBlur();
            },

            onAnimationEnd() {
                this.$emit(this.show ? 'show' : 'hide');
            },

            onPress(text) {
                if (text === '') {
                    return;
                }

                if (text === this.deleteText) {
                    this.$emit('delete');
                } else if (text === this.closeButtonText) {
                    this.onClose();
                } else {
                    this.$emit('input', text);
                }
            },
            bem: utils.bem,
            stop: stop,
        },
    }
</script>

