<template>
    <div
            :class="[
                    bem('nav-bar', [{ fixed: fixed }]),
                    { 'jst-hairline--bottom': border }
            ]"
            :style="{ zIndex: zIndex }"
    >
        <div class="jst-nav-bar__left" @click="onClickLeft">
            <slot name="left">
                <jst-icon v-if="leftArrow" class="jst-nav-bar__arrow" name="arrow-left"></jst-icon>
                <span v-if="leftText" class="jst-nav-bar__text">{{leftText}}</span>
            </slot>
        </div>
        <div :class="['jst-nav-bar__title', 'jst-ellipsis']">
            <slot name="title">{{title}}</slot>
        </div>
        <div class="jst-nav-bar__right" @click="onClickRight">
            <slot name="right">
                <span class="jst-nav-bar__text">{{rightText}}</span>
            </slot>
        </div>
    </div>
</template>

<script>
    import utils from "./../tool/utils";

    export default {
        name: "jst-nav-bar",

        props: {
            title: String,
            fixed: Boolean,
            leftText: String,
            rightText: String,
            leftArrow: Boolean,
            border: {
                type: Boolean,
                default: true
            },
            zIndex: {
                type: Number,
                default: 1
            }
        },

        methods: {
            bem: utils.bem,
            onClickLeft() {
                this.$emit('click-left');
            },

            onClickRight() {
                this.$emit('click-right');
            }
        }
    }
</script>

