<template>
    <div :class="['jst-collapse-item', { 'jst-hairline--top': index }]" >
        <jst-cell
                :title="title"
                :icon="icon"
                :value="value"
                :label="label"
                :is-link="isLink"
                :border="border && expanded"
                :class="bem('collapse-item__title', { disabled: disabled, expanded: expanded })"
                @click="onClick"
        >
            <slot name="title" slot="title"></slot>
            <slot name="icon" slot="icon"></slot>
            <slot name="value"></slot>
            <slot name="right-icon" slot="right-icon"></slot>
        </jst-cell>

        <transition @transitionend="onTransitionEnd">
            <div
                    v-show="show"
                    ref="wrapper"
                    class="jst-collapse-item__wrapper"

            >
                <div ref="content" class="jst-collapse-item__content">
                    <slot></slot>
                </div>
            </div>
        </transition>

    </div>
</template>

<script>
    import { isDef } from '../tool/tools';
    import { raf } from '../tool/raf';
    import utils from '../tool/utils'

    import { cellProps } from '../cell/shared';
    import { FindParentMixin } from '../mixins/find-parent';

    const CELL_SLOTS = ['title', 'icon', 'right-icon'];

    export default {
        name: "jst-collapse-item",

        mixins: [FindParentMixin],

        props: {
            ...cellProps,
            name: [String, Number],
            disabled: Boolean,
            isLink: {
                type: Boolean,
                default: true
            }
        },

        data() {
            return {
                show: null,
                inited: null
            };
        },

        computed: {
            items() {
                return this.parent.items;
            },

            index() {
                return this.items.indexOf(this);
            },

            currentName() {
                return isDef(this.name) ? this.name : this.index;
            },

            expanded() {
                if (!this.parent) {
                    return null;
                }

                const { value } = this.parent;
                return this.parent.accordion
                    ? value === this.currentName
                    : value.some(name => name === this.currentName);
            }
        },

        created() {
            this.findParent('jst-collapse');
            this.items.push(this);
            this.show = this.expanded;
            this.inited = this.expanded;
        },

        destroyed() {
            this.items.splice(this.index, 1);
        },

        watch: {
            expanded(expanded, prev) {
                if (prev === null) {
                    return;
                }

                if (expanded) {
                    this.show = true;
                    this.inited = true;
                }

                raf(() => {
                    const { content, wrapper } = this.$refs;
                    if (!content || !wrapper) {
                        return;
                    }

                    const { clientHeight } = content;
                    if (clientHeight) {
                        const contentHeight = `${clientHeight}px`;
                        wrapper.style.height = expanded ? 0 : contentHeight;
                        raf(() => {
                            wrapper.style.height = expanded ? contentHeight : 0;
                        });
                    } else {
                        this.onTransitionEnd();
                    }
                });
            }
        },

        methods: {
            bem: utils.bem,
            onClick() {
                if (this.disabled) {
                    return;
                }

                const { parent } = this;
                const name =
                    parent.accordion && this.currentName === parent.value
                        ? ''
                        : this.currentName;
                const expanded = !this.expanded;
                this.parent.switch(name, expanded);
            },

            onTransitionEnd() {
                if (!this.expanded) {
                    this.show = false;
                } else {
                    this.$refs.wrapper.style.height = null;
                }
            }
        }
    }
</script>

