<template>
    <i
            :class="className + ' jst-hairline ' + 'jst-key'"
            @touchstart="onFocus"
            @touchmove="onBlur"
            @touchend="onBlur"
            @touchcancel="onBlur"
    >
        {{text}}
    </i>
</template>

<script>
    import utils from './../tool/utils'
    export default {
        name: "jst-keys",
        data(){
            return{
                active: false
            }
        },
        props: {
            type: {
                type: Array,
                default(){
                    return []
                }
            },
            text: [String, Number]
        },
        computed: {
            className() {
                const types = this.type.slice(0);
                this.active && types.push('key--active ');
                return types.length > 0 ? utils.bem(types) : '';
            }
        },

        methods: {
            onFocus() {
                this.active = true;
                this.$emit('press', this.text);
            },

            onBlur(event) {
                event.preventDefault();
                event.stopPropagation();
                this.active = false;
            },
            bem: utils.bem
        },
    }
</script>

