<template>
    <div class="jst-circle" :style="style">
        <svg viewBox="0 0 1060 1060">
            <path class="jst-circle__hover" :style="hoverStyle" d="M 530 530 m -500, 0 a 500, 500 0 1, 1 1000, 0 a 500, 500 0 1, 1 -1000, 0"/>
            <path class="jst-circle__layer" :style="layerStyle" d="M 530 530 m -500, 0 a 500, 500 0 1, 1 1000, 0 a 500, 500 0 1, 1 -1000, 0"/>
        </svg>
        <div
                class="jst-circle__text"
                v-if="text"
        >
            {{ text }}
        </div>
        <slot v-else/>
    </div>
</template>


<script>
    import { raf, cancel } from "./../tool/raf";
    import utils from './../tool/utils';
    import { BLUE, WHITE } from './../tool/color';


    const PERIMETER = 3140;

    function format(rate) {
        return Math.min(Math.max(rate, 0), 100);
    }

    export default {
        name: "jst-circle",

        props:{
            text: String,
            value: Number,
            speed: Number,
            size: {
                type: String,
                default: '100px'
            },
            fill: {
                type: String,
                default: 'none'
            },
            rate: {
                type: Number,
                default: 100
            },
            layerColor: {
                type: String,
                default: WHITE
            },
            color: {
                type: String,
                default: BLUE
            },
            strokeWidth: {
                type: Number,
                default: 40
            },
            clockwise: {
                type: Boolean,
                default: true
            }
        },

        computed:{
            style() {
                return {
                    width: this.size,
                    height: this.size
                };
            },

            layerStyle() {
                let offset = PERIMETER * (100 - this.value) / 100;
                offset = this.clockwise ? offset : PERIMETER * 2 - offset;
                return {
                    stroke: "" + this.color,
                    strokeDashoffset: offset + "px",
                    strokeWidth: this.strokeWidth + 1 + "px"
                };
            },

            hoverStyle() {
                return {
                    fill: "" + this.fill,
                    stroke: "" + this.layerColor,
                    strokeWidth: this.strokeWidth + "px"
                }
            },
        },

        watch: {
            rate: {
                handler() {
                    this.startTime = Date.now();
                    this.startRate = this.value;
                    this.endRate = format(this.rate);
                    this.increase = this.endRate > this.startRate;
                    this.duration = Math.abs(((this.startRate - this.endRate) * 1000) / this.speed);
                    if (this.speed) {
                        cancel(this.rafId);
                        this.rafId = raf(this.animate);
                    } else {
                        this.$emit('input', this.endRate);
                    }
                },
                immediate: true
            }
        },

        methods:{
            animate() {
                const now = Date.now();
                const progress = Math.min((now - this.startTime) / this.duration, 1);
                const rate = progress * (this.endRate - this.startRate) + this.startRate;
                this.$emit('input', format(parseFloat(rate.toFixed(1))));
                if (this.increase ? rate < this.endRate : rate > this.endRate) {
                    this.rafId = raf(this.animate);
                }
            },

            bem:utils.bem
        },
    }
</script>

<style scoped>
    .jst-circle {
        position: relative;
        text-align: center;
        display: inline-block;
    }
    .jst-circle svg {
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        position: absolute;
    }
    .jst-circle__layer {
        fill: none;
        stroke-linecap: round;
        stroke-dasharray: 3140;
        stroke-dashoffset: 3140;
        transform: rotate(90deg);
        transform-origin: 530px 530px;
    }
    .jst-circle__text {
        top: 50%;
        left: 0;
        width: 100%;
        color: #323233;
        position: absolute;
        transform: translateY(-50%);
    }
</style>
