<template>
    <i
            :class="iconClass"
            :style="'position: relative;' + (color ? 'color: ' + color + ';' : '') + (size ? 'font-size: ' + size + ';' : '') +  customStyle"
            @click="onClick"
    >
        <!--<span-->
                <!--v-if="info"-->
                <!--class="jst-info jst-icon__info"-->
        <!--&gt;-->
            <!--{{info}}-->
        <!--</span>-->
        <jst-info v-if="info" :info="info" class="jst-icon__info" ></jst-info>
        <img
                v-if="isSrc(name)"
                :src="name"
                class="jst-icon__image"
        />
    </i>
</template>

<script>
    import utils from '../tool/utils';

    export default {
        name: "jst-icon",

        props: {
            info: null,
            name: {
                type: String,
                default: ''
            },// iconfont 图标类名称
            size: String,
            color: String,
            customStyle: String,
            classPrefix: {
                type: String,
                default: 'iconfont'
            },
            customClass: {
                type: String,
                default: ''
            }
        },

        computed: {
          iconClass(){
              let name = this.name;
              // if(this.name=="success") name = "icon-duihao";
              // if(this.name=="fail") name = "icon-jinggao1";
              // if(this.name=="checked") name = "icon-chenggong";
              // if(this.name=="arrow") name = "icon-you";
              // if(this.name=="arrow-left") name: "icon-zuo-";
              let temp = {
                  "success": "icon-duihao",
                  "fail": "icon-jinggao1",
                  "checked": "icon-chenggong",
                  "checkeds": "icon-wancheng",
                  "arrow": "icon-you",
                  "arrow-left": "icon-zuo-",
                  "search": "icon-sousuo5"
              };
              if(!utils.isSrc(name) && temp[name]){
                  name = temp[name];
              }
              return `${utils.isSrc(this.name) ? '' : this.classPrefix + ' ' + name } ${this.customClass}`;
          }
        },

        methods: {
            onClick() {
                this.$emit('click');
            },
            isSrc: utils.isSrc
        }

    }
</script>

